import { DefinitionIndeCanvasCore } from '../../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '../..';
import { BoidContollerWander0 } from './BoidCore/BoidContollerWander0';

export class BoidSystemWander0 extends DefinitionIndeCanvasCore {
    mouse: number[] = [0, 0];

    BoidContoller: BoidContollerWander0;
    dragTime = 0;
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.indeCanvas.title = 'BoidSystem01';
        this.init();
    }
    init() {
        this.BoidContoller = new BoidContollerWander0();
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#eeeeee';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        this.BoidContoller.Update();
        this.BoidContoller.Render(ctx);
    }
    MouseMove(x: number, y: number) {
        this.mouse = [x, y];
    }
    MouseDown() {
        this.BoidContoller.AddAgent(this.mouse[0], this.mouse[1], this.width, this.height);
        // if (this.lines.length > 5) { this.lines.shift(); }
        // const p1 = this.lines[this.lines.length - 1].p1;
        // this.lines.push(new Line(p1[0], p1[1], this.mouse[0], this.mouse[1] ));
    }
    MouseDrag(x: number, y: number) {
        if (this.dragTime > 5) {
            this.BoidContoller.AddAgent(x, y, this.width, this.height);
            this.dragTime = 0;
        }
        this.dragTime++;
    }

}