import { DefinitionIndeCanvasCore } from '../../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '../..';

// import { NVector3 } from '../../../../lib';
import { Scene } from './Scene';
import { Core } from './Core';

export class MatterSandbox extends DefinitionIndeCanvasCore {
    mouse: number[] = [0, 0];

    sceneMatter: Scene;
    core: Core;
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.indeCanvas.title = 'MatterSandbox';
        console.log('init MatterSandbox');
        this.core = new Core(this);
        this.init();
    }
    public init() {
        // let v = new NVector3(this.width * 0.5, this.height * 0.5, 0);
        this.sceneMatter = new Scene(this);
    }
    public RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.sceneMatter.Update();
        this.sceneMatter.Render(ctx);
    }
    public MouseMove(x: number, y: number) {
        this.mouse = [x, y];
    }
    public MouseDrag(x: number, y: number) {
        this.sceneMatter.MouseDrag(x, y);
    }
    public MouseDown(x: number, y: number) {
        this.sceneMatter.MouseDown(x, y);
    }

}
