import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class AlgorithmNumOfArea extends DefinitionIndeCanvasCore {
    p: number[] = [0, 0];
    map: PixelMap;
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.init();

    }
    init() {
        const size = [5, 7];
        const array = [
            [0, 2, 4, 4],
            [1, 1, 2, 5],
            [4, 0, 6, 2]
        ];
        let before = performance.now();
        this.map = new PixelMap(size[1], size[0], array);
        let after = performance.now();
        console.log('time: ', after - before);
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.map.Render(ctx, this.p);
        ctx.fillStyle = '#ff0000';
        ctx.beginPath();
        ctx.arc(this.p[0], this.p[1], 5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();

    }
    MouseMove(x: number, y: number) {
        this.p = [x, y];
    }
    MouseDown(x: number, y: number) {
        this.p = [x, y];
    }
}

// https://www.acmicpc.net/problem/1012

class Pixel {
    x: number = -1;
    y: number = -1;
    scale: number = 50;
    fill: boolean = false;
    visited: boolean = false;
    connections: Pixel[] = [];
    constructor(x: number, y: number) {
        this.x = x;
        this.y = y;
    }
    Render(ctx: CanvasRenderingContext2D, p: number[]) {
        let xPos = (this.x + .5) * this.scale;
        let yPos = (this.y + .5) * this.scale;

        const dis = Math.sqrt((p[0] - xPos) * (p[0] - xPos) + (p[1] - yPos) * (p[1] - yPos));

        ctx.lineWidth = 4;
        ctx.beginPath();
        ctx.rect(this.x * this.scale, this.y * this.scale, this.scale, this.scale);
        ctx.closePath();
        if (this.fill) {
            if (this.visited) {
                ctx.fillStyle = 'rgba(255,0,0, 0.5)';
            } else {
                ctx.fillStyle = 'rgba(0,255,0, 0.5)';
            }
            ctx.fill();
        }
        ctx.stroke();

        if (dis < 0.5 * this.scale) {
            ctx.lineWidth = 1;
            for (let i = 0; i < this.connections.length; ++i) {
                ctx.beginPath();
                ctx.moveTo((this.x + .5) * this.scale, (this.y + .5) * this.scale);
                ctx.lineTo((this.connections[i].x + .5) * this.scale, (this.connections[i].y + .5) * this.scale);
                // ctx.closePath();
                ctx.stroke();
            }
        }
    }
    Add(p: Pixel) {
        if (p.fill) { return; }
        this.connections.push(p);
    }
}
class PixelMap {
    pixels: Pixel[][] = [];
    constructor(xNum: number, yNum: number, num: number[][]) {
        for (let y = 0; y < yNum; ++y) {
            const pixelsRow = [];
            for (let x = 0; x < xNum; ++x) {
                pixelsRow.push(new Pixel(x, y));
            }
            this.pixels.push(pixelsRow);
        }
        this.InitFill(num);
        this.InitConnections();
        this.Compute();
    }
    Compute() {
        for (let y = 0; y < this.pixels.length; ++y) {
            for (let x = 0; x < this.pixels[0].length; ++x) {
                let pixel = this.pixels[y][x];
                if (!pixel.fill && !pixel.visited) {
                    let count = this.PropagationBFS(pixel);
                    console.log(count);
                }
            }
        }
    }
    PropagationBFS(p: Pixel) {
        let queue = [p];
        let count = 0;
        while (queue.length > 0) {
            let current = queue[0];
            queue.shift();
            if (current.visited) { continue; }
            current.visited = true;
            count++;
            for (let i = 0; i < current.connections.length; ++i) {
                if (!current.connections[i].visited) {
                    queue.push(current.connections[i]);
                }
            }
        }
        return count;
    }
    InitFill(num: number[][]) {
        for (let i = 0; i < num.length; ++i) {
            let xMin = num[i][0];
            let yMin = num[i][1];
            let xMax = num[i][2];
            let yMax = num[i][3];
            for (let y = yMin; y < yMax; ++y) {
                for (let x = xMin; x < xMax; ++x) {
                    this.pixels[y][x].fill = true;
                }
            }
        }
    }
    InitConnections() {
        for (let y = 0; y < this.pixels.length; ++y) {
            for (let x = 0; x < this.pixels[0].length; ++x) {
                const theCell = this.pixels[y][x];
                if (y !== 0 && y < this.pixels.length - 1 && x !== 0 && x < this.pixels[0].length - 1) {
                    theCell.Add(this.pixels[y - 1][x]);
                    theCell.Add(this.pixels[y + 1][x]);
                    theCell.Add(this.pixels[y][x - 1]);
                    theCell.Add(this.pixels[y][x + 1]);
                } else if (y === 0) {
                    theCell.Add(this.pixels[y + 1][x]);
                    if (0 < x && x < this.pixels[0].length - 1) {
                        theCell.Add(this.pixels[y][x - 1]);
                        theCell.Add(this.pixels[y][x + 1]);
                    } else if (x === 0) {
                        theCell.Add(this.pixels[y][x + 1]);
                    } else if (x === this.pixels[0].length - 1) {
                        theCell.Add(this.pixels[y][x - 1]);
                    }
                } else if (y === this.pixels.length - 1) {
                    theCell.Add(this.pixels[y - 1][x]);
                    if (0 < x && x < this.pixels[0].length - 1) {
                        theCell.Add(this.pixels[y][x - 1]);
                        theCell.Add(this.pixels[y][x + 1]);
                    } else if (x === 0) {
                        theCell.Add(this.pixels[y][x + 1]);
                    } else if (x === this.pixels[0].length - 1) {
                        theCell.Add(this.pixels[y][x - 1]);
                    }
                } else if (x === 0) {
                    theCell.Add(this.pixels[y][x + 1]);
                    if (0 < y && y < this.pixels.length - 1) {
                        theCell.Add(this.pixels[y - 1][x]);
                        theCell.Add(this.pixels[y + 1][x]);
                    } else if (y === 0) {
                        theCell.Add(this.pixels[y + 1][x]);
                    } else if (y === this.pixels.length - 1) {
                        theCell.Add(this.pixels[y - 1][x]);
                    }
                } else if (x === this.pixels[0].length - 1) {
                    theCell.Add(this.pixels[y][x - 1]);
                    if (0 < y && y < this.pixels.length - 1) {
                        theCell.Add(this.pixels[y - 1][x]);
                        theCell.Add(this.pixels[y + 1][x]);
                    } else if (y === 0) {
                        theCell.Add(this.pixels[y + 1][x]);
                    } else if (y === this.pixels.length - 1) {
                        theCell.Add(this.pixels[y - 1][x]);
                    }
                }
            }
        }
        // console.log(this);
    }
    Render(ctx: CanvasRenderingContext2D, p: number[]) {
        for (let y = 0; y < this.pixels.length; ++y) {
            for (let x = 0; x < this.pixels[0].length; ++x) {
                this.pixels[y][x].Render(ctx, p);
            }
        }
    }
}