class Point {

    age: number;
    height: number;
    weight: number;

    // money: ComputeData;

    constructor(age: number, height: number, weight: number) {
        this.age = age;
        this.height = height;
        this.weight = weight;
        // this.money = new ComputeData();

    }
    Render(ctx: CanvasRenderingContext2D) {
        ctx.beginPath();
        ctx.arc(this.age, this.height, this.weight, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();
        this.age += 0.01;

        if (this.age > 102) { this.Compute(); }
    }
    Compute() {
        this.height += 0.1;
    }

}

