import { NVector3 } from '../../../../../';
import { MouseEventData } from '../../../../../IO/EventInteraction/MouseInteraction';

import { IndependentPanelWrapper } from '../../IndependentPanelWrapper';
import { ColorUtility } from '../../../../../Core/NColor/';

import { IndePanelPreset } from '../../Core/IndePanelPreset';

import { IndePanelBase } from './base/base/IndePanelBase';

import { BtnClose } from './base/BtnClose';
import { BtnMinimize } from './base/BtnMinimize';
import { BtnHam } from './base/BtnHam';
import { BtnResize } from './base/BtnResize';

import { BodyDiv } from './base/BodyDiv';
import { IndeSubPanel } from './base/IndeSubPanel';

export class IndependentPanelCanvas extends IndePanelBase {
    independentPanelWrapper: IndependentPanelWrapper;
    divInput: HTMLTextAreaElement;

    btnClose: BtnClose;
    btnMini: BtnMinimize;
    btnHam: BtnHam;
    btnResize: BtnResize;

    bodyDiv: BodyDiv;
    subMenu: IndeSubPanel;

    subs: IndePanelBase[] = [];

    _content: string;

    canvas: HTMLCanvasElement;
    ctx: CanvasRenderingContext2D;
    isActive: boolean = true;

    constructor(independentPanelWrapper: IndependentPanelWrapper, x: number = 0, y: number = 0, width: number = 300, height: number = 170, divNeeded: boolean = false) {
        super(independentPanelWrapper);
        this.independentPanelWrapper = independentPanelWrapper;
        this.title = 'njslabSandbox V3';
        this.width = width;
        this.height = height;
        if (x === 0 && y === 0) {
            this.vec.x = (independentPanelWrapper.width * 0.5) - (this.width * 0.5);
            this.vec.y = (independentPanelWrapper.height * 0.5) - (this.height * 0.5);
        } else {
            this.vec.x = x;
            this.vec.y = y;
        }
        this.filletR = 7;
        this.InitTop();
        if (divNeeded) {
            this.bodyDiv = new BodyDiv(this, this.vec.x, this.vec.y + this.HeadHeight, this.width, this.height - this.HeadHeight);
            // this.bodyDiv.div.style.background = 'red';
        }
        this.InitCanvas(this.bodyDiv.div);
    }

    get IsMinimized() {
        return this.isMinimized;
    }
    set IsMinimized(v: boolean) {
        this.isMinimized = v;
        this.btnResize.isHidden = v;
        if (this.bodyDiv) { this.bodyDiv.Hide(v); }
    }
    get Width() {
        return this.width;
    }
    set Width(v: number) {
        let off = v - this.width;
        this.width = v;
        this.btnHam.vec.x += off;
        this.subMenu.vec.x += off;
        if (this.bodyDiv) { this.bodyDiv.Width = v; }
    }
    get Height() {
        return this.height;
    }
    set Height(v: number) {
        this.height = v;
        if (this.bodyDiv) { this.bodyDiv.Height = v; }
        // this.subMenu
    }
    get content() {
        return this._content;
    }
    set content(v: string) {
        this._content = v;
        if (this.bodyDiv) { this.bodyDiv.content = v; }
    }
    get HasSubMenu() {
        return this.hasSubMenu;
    }
    set HasSubMenu(v: boolean) {
        this.hasSubMenu = v;
        this.subMenu.IsHidden = !v;
    }
    InitTop() {
        this.btnClose = new BtnClose(this, this.vec.x, this.vec.y);
        this.btnMini = new BtnMinimize(this, this.vec.x + 25, this.vec.y);
        this.btnHam = new BtnHam(this, this.vec.x - 25 + this.width, this.vec.y);
        this.btnResize = new BtnResize(this, this.vec.x + this.width - 10, this.vec.y + this.height - 10, 10, 10);

        this.subMenu = new IndeSubPanel(this);
        this.subs.push(this.btnClose, this.btnMini, this.btnHam);
    }
    Translate(e: MouseEventData): void {
        if (!this.isMoveable) { return; }
        let xo = e.x - e.preX;
        let yo = e.y - e.preY;
        this.vec.x += xo;
        this.vec.y += yo;
        for (let i = 0; i < this.boundarVecs.length; ++i) { this.boundarVecs[i].x += xo; this.boundarVecs[i].y += yo; }
        for (let i = 0; i < this.subs.length; ++i) { this.subs[i].Translate(e); }
        this.btnResize.vec.x += xo;
        this.btnResize.vec.y += yo;
        this.subMenu.Translate(xo, yo);
        if (this.bodyDiv) { this.bodyDiv.Translate(xo, yo); }

    }
    Remove() {
        this.RemovePost();
        for (let i = 0; i < this.subs.length; ++i) { this.subs[i].Remove(); }
        if (this.bodyDiv) { this.bodyDiv.Remove(); }
        this.btnResize.Remove();
        this.independentPanelWrapper.indePanelRender.Remove(this);
    }
    RenderPost(ctx: CanvasRenderingContext2D): void {
        // console.log(IndePanelRender.length);
        // this.vec.x = this.mainPanel.vec.x + this.xOff;
        // this.vec.y = this.mainPanel.vec.y + this.yOff;
        this.ComputeBoundary();

        let color;
        // if (this.isOver) {
        //     color = ColorUtility.HexToCanvasColor(IndePanelPreset.theme.mainMenuOver, this.transparent);
        // } else {
        color = ColorUtility.HexToCanvasColor(IndePanelPreset.theme.mainMenuNormal, this.transparent);
        // }
        if (this.isMinimized) {
            IndePanelPreset.TopRoundedRectFill(ctx, this.title, NVector3.GetBoundaryByVecWidthHeight(this.vec, this.width, this.HeadHeight), this.filletR, 0, this.isOver, this.isSelected, this.theme.colSelectedBG, false);

        } else {
            IndePanelPreset.RoundedRectFillWithColorMainMenu(ctx, this.title, this.boundarVecs, this.filletR, 0, this.isOver, this.isSelected, color);
            IndePanelPreset.TopRoundedRectFill(ctx, this.title, NVector3.GetBoundaryByVecWidthHeight(this.vec, this.width, this.HeadHeight), this.filletR, 0, this.isOver, this.isSelected, this.theme.colSelectedBG, false);
        }
        this.RenderHeadTitle(ctx);
        if (!this.isMinimized) {
            this.RenderBodyContent(ctx);
        }
        if (this.hasSubMenu) {
            this.subMenu.RenderPost(ctx);
        }
        this.RenderBtnHorizontal(ctx);
    }
    RenderBtnHorizontal(ctx: CanvasRenderingContext2D) {
        /* */
    }
    RenderHeadTitle(ctx: CanvasRenderingContext2D) {
        ctx.save();
        // if (this.isOver) {
        //     ctx.fillStyle = IndePanelPreset.theme.mainMenuNormal;
        // } else {
        ctx.fillStyle = '#ffffff';
        // }
        ctx.textBaseline = 'top';
        ctx.textAlign = 'center';
        ctx.font = '100 12px Arial';
        ctx.shadowBlur = 0;
        // ctx.shadowColor = 'rgba(0,0,0,0.3)';
        ctx.shadowOffsetY = 0;
        ctx.shadowOffsetX = 0;
        ctx.fillText(this.title, this.vec.x + (this.width * 0.5), this.vec.y + (this.HeadHeight * 0.5) - 7);

        ctx.restore();
    }
    RenderBodyContent(ctx: CanvasRenderingContext2D) {
        // ctx.save();
        // // if (this.isOver) {
        // //     ctx.fillStyle = IndePanelPreset.theme.mainMenuNormal;
        // // } else {
        // ctx.fillStyle = '#000000';
        // // }
        // ctx.textBaseline = 'top';
        // ctx.textAlign = 'center';
        // ctx.font = '100 9px Sans-serif';
        // ctx.fillText(this.content, this.vec.x + (this.width * 0.5), this.vec.y + (this.height * 0.5) - 5);

        // ctx.shadowBlur = 0;
        // // ctx.shadowColor = 'rgba(0,0,0,0.3)';
        // ctx.shadowOffsetY = 0;
        // ctx.shadowOffsetX = 0;

        // ctx.restore();

    }
    RemovePost() {
        this.isActive = false;
    }
    InitCanvas(hostDiv: HTMLElement) {
        this.canvas = document.createElement('canvas');
        this.canvas.style.position = 'absolute';
        this.canvas.style.zIndex = '0';
        hostDiv.appendChild(this.canvas);

        this.canvas.id = 'NCanvasRenderer';
        this.canvas.className = 'njslab-web-core-canvas-renderer';
        this.canvas.style.pointerEvents = 'none';
        this.ctx = this.canvas.getContext('2d') as CanvasRenderingContext2D;

        this.canvas.width = hostDiv.clientWidth; // hostDivForSize.clientWidth;
        this.canvas.height = hostDiv.clientHeight; // hostDivForSize.clientHeight;

        this.btnResize.ResizePropagation = (width: number, height: number) => {
            this.canvas.width = width; // hostDivForSize.clientWidth;
            this.canvas.height = height; // hostDivForSize.clientHeight;
        };
        this.LoopIndependentCanvas(this.ctx);
    }
    LoopIndependentCanvas(ctx: CanvasRenderingContext2D) {
        if (!this.isActive) { return; }
        ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
        requestAnimationFrame(() => { this.LoopIndependentCanvas(ctx); });
        this.RenderIndependentCanvas(ctx);
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        console.log('implementation needed');
    }
    ResizeCanvas(width: number, height: number) {
        this.canvas.width = width; // hostDivForSize.clientWidth;
        this.canvas.height = height; // hostDivForSize.clientHeight;
    }
}