import { DefinitionIndeCanvasCore } from '../../Core/DefinitionIndeCanvasCore';
import { NVector3 } from '../../../../lib';
import { CanvasDrawing } from '../../../../../../../NJSCore';
import { SceneCreativeCoding } from '../..';

export class CustomDynamicBin extends DefinitionIndeCanvasCore {
    data: NVector3[] = [];
    p: number[] = [0, 0];
    pixelMap: CustomDynamicBinSystem;
    captureVec: NVector3 | undefined = undefined;
    dataHtml: HTMLElement;
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);

        this.init();
        this.InitUI();
    }
    public InitUI() {
        const div = document.createElement('div');
        // div.textContent = 'ccc';
        // div.style.background = '#ff0000';
        div.style.position = 'absolute';
        div.style.marginLeft = '10px';
        // div.style.float = 'left';

        let p = document.createElement('div');
        if (!p) { return; } div.append(p);
        // p.style.width = 'max-content';
        p.style.cssFloat = 'left';
        p.innerHTML = 'x res: ';

        let input = document.createElement('input');
        input.oninput = () => {
            const val = +input.value as number;
            this.pixelMap.rx = (Math.floor(val) % 2) === 0 ? Math.floor(val) : Math.floor(val) + 1;
            // console.log((Math.floor(val) % 2) === 0 ? Math.floor(val) : Math.floor(val) + 1);
            this.pixelMap.Init();
        };
        input.innerText = 'x res: ';
        input.type = 'range';
        input.min = '6';
        input.max = '40';
        input.value = '15';
        input.style.cssFloat = 'left';
        input.style.marginRight = '25px';
        div.append(input);

        p = document.createElement('div');
        if (!p) { return; } div.append(p);
        // p.style.width = 'max-content';
        p.style.cssFloat = 'left';
        p.innerHTML = 'y res: ';

        let input2 = document.createElement('input');
        input2.oninput = () => {
            const val = +input2.value as number;
            this.pixelMap.ry = (Math.floor(val) % 2) === 0 ? Math.floor(val) : Math.floor(val) + 1;
            // console.log((Math.floor(val) % 2) === 0 ? Math.floor(val) : Math.floor(val) + 1);
            this.pixelMap.Init();
        };
        input2.type = 'range';
        input2.min = '6';
        input2.max = '40';
        input2.value = '15';
        input2.style.cssFloat = 'left';
        div.append(input2);

        p = document.createElement('div');
        if (!p) { return; } div.append(p);
        // p.style.width = 'max-content';
        p.style.cssFloat = 'left';
        p.style.marginLeft = '10px';
        p.innerHTML = 'Mode: ';

        let input3 = document.createElement('input');
        input3.oninput = () => {
            const val = +input3.value as number;
            this.pixelMap.mode = val;
            this.pixelMap.Init();
        };
        input3.type = 'range';
        input3.min = '0';
        input3.max = '5';
        input3.value = '0';
        input3.style.cssFloat = 'left';
        input3.style.width = '50px';
        div.append(input3);

        p = document.createElement('div');
        if (!p) { return; } div.append(p);
        // p.style.width = 'max-content';
        p.style.cssFloat = 'left';
        p.style.marginLeft = '10px';
        p.innerHTML = 'data : ';
        this.dataHtml = p;

        this.scene.implementation.app.divHost.append(div);
    }
    public init() {
        this.pixelMap = new CustomDynamicBinSystem(new NVector3(100, 100), new NVector3(400, 300));

        const off = 5;
        this.data = this.InitData(800, 0 + off, this.width - off, 20 + off, this.height - off, 0.5, 4);
        // console.log(this.data);
    }
    public InitData(num: number, xMin: number, xMax: number, yMin: number, yMax: number, zMin: number, zMax: number) {
        const data: NVector3[] = [];
        for (let i = 0; i < num; ++i) {
            const x = xMin + (Math.random() * xMax);
            const y = yMin + (Math.random() * yMax);
            const z = zMin + (Math.random() * zMax);
            data.push(new NVector3(x, y, z));
        }
        return data;
    }
    public MouseDown(x: number, y: number) {
        this.captureVec = this.pixelMap.IsHover(x, y);
    }
    public MouseMove(x: number, y: number) {
        this.pixelMap.MoveInspection(x, y);
        if (this.pixelMap.selPixel) {
            const d = (this.pixelMap.selPixel.data['num'] as number).toFixed(4);
            this.dataHtml.innerText = 'data: ' + d;
            return;
        }
        this.dataHtml.innerText = 'data: ';
    }
    public MouseDrag(x: number, y: number) {
        if (this.captureVec) {
            this.pixelMap.DragControlPoint(x, y);

        }
    }
    public MouseUp(x: number, y: number) {
        this.captureVec = undefined;
    }
    public RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        // console.log(ctx);

        for (let i = 0; i < this.data.length; ++i) {
            CanvasDrawing.PointByNVec(ctx, this.data[i], this.data[i].z, 'rgba(0, 0, 0, 0.5)');
        }

        this.pixelMap.UpdateData(this.data);

        // for (let i = 0; i < this.pixelMap.pts.length; ++i) {
        //     for (let j = 0; j < this.pixelMap.pts[i].length; ++j) {
        //         CanvasDrawing.PointByNVec(ctx, this.pixelMap.pts[i][j], 4, 'rgba(0, 0, 0, 1)');
        //     }
        // }
        this.pixelMap.RenderPixel(ctx);
        this.pixelMap.RenderCorner(ctx);
        this.pixelMap.RenderSelPixel(ctx);
        // console.log(this.pixelMap.pts);
    }
}


export class CustomDynamicBinSystem {
    v0: NVector3;
    v1: NVector3;
    rx: number;
    ry: number;
    ddx: number;
    ddy: number;
    pts: NVector3[][] = [];
    ptList: NVector3[] = [];
    listPixels: Pixel[] = [];
    m: NVector3;
    t: number = 0.0;
    mode: number = 0;
    selPixel: Pixel | undefined = undefined;
    constructor(v0: NVector3, v1: NVector3) {
        this.v0 = v0;
        this.v1 = v1;
        this.rx = 20;
        this.ry = 20;
        this.ddx = 0.0;
        this.ddy = 0.0;
        this.m = new NVector3(0, 0);
        this.Init();
    }
    public IsHover(x: number, y: number) {
        this.m.x = x; this.m.y = y;
        let p = this.pts[0][0];
        if (NVector3.Distance(this.m, p) < 8) { return p; }
        p = this.pts[0][this.pts[0].length - 1];
        if (NVector3.Distance(this.m, p) < 8) { return p; }
        p = this.pts[this.pts.length - 1][this.pts[0].length - 1];
        if (NVector3.Distance(this.m, p) < 8) { return p; }
        p = this.pts[this.pts.length - 1][0];
        if (NVector3.Distance(this.m, p) < 8) { return p; }
        return undefined;
    }
    public Move(x: number, y: number) {
        this.MoveInspection(x, y);
    }
    public MoveInspection(x: number, y: number) {
        this.m.x = x;
        this.m.y = y;
        for (let j = 0; j < this.listPixels.length; ++j) {
            // console.log('ddd');
            if (NVector3.IsInsideOfVecs(this.m, this.listPixels[j].recVec)) {
                this.selPixel = this.listPixels[j];
                return;
            } else {
                this.selPixel = undefined;
                // console.log('out');
            }
        }
    }
    public DragControlPoint(x: number, y: number) {
        this.m.x = x; this.m.y = y;
        let p = this.pts[0][0];
        if (NVector3.Distance(this.m, p) < 60) {
            this.v0.x = x;
            this.v0.y = y;
        }
        p = this.pts[0][this.pts[0].length - 1];
        if (NVector3.Distance(this.m, p) < 60) {
            this.v1.x = x;
            this.v0.y = y;
        }
        p = this.pts[this.pts.length - 1][this.pts[0].length - 1];
        if (NVector3.Distance(this.m, p) < 60) {
            this.v1.x = x;
            this.v1.y = y;
        }
        p = this.pts[this.pts.length - 1][0];
        if (NVector3.Distance(this.m, p) < 60) {
            this.v0.x = x;
            this.v1.y = y;
        }
        this.Init();
    }
    public Init() {
        this.ddx = (this.v1.x - this.v0.x) / (this.rx - 1.0);
        this.ddy = (this.v1.y - this.v0.y) / (this.ry - 1.0);
        // console.log('ddx is ' + this.ddx + '. ddy is ' + this.ddx);
        // console.log('Pixe Map was executed...');
        this.pts = [];
        this.ptList = [];
        this.listPixels = [];

        this.InitGrid();
        // this.ComputeAtt();

        switch (this.mode) {
            case 0: this.ComputeRec(); break;
            case 1: this.ComputeTriA(); break;
            case 2: this.ComputeTriB(); break;
            case 3: this.ComputeDia(); break;
            case 4: this.ComputeLShape(); break;
            default: this.ComputeHex(); break;
        }

    }
    public UpdateData(vs: NVector3[]) {
        for (let j = 0; j < this.listPixels.length; ++j) {
            this.listPixels[j].data['num'] = 0;
        }
        for (let i = 0; i < vs.length; ++i) {
            const p = vs[i];
            for (let j = 0; j < this.listPixels.length; ++j) {
                if (NVector3.IsInsideOfVecs(p, this.listPixels[j].recVec)) {
                    this.listPixels[j].data['num'] += p.z;
                }
            }
        }
    }
    public InitGrid() {
        this.pts = [];
        for (let j = 0; j < this.ry; ++j) {
            let y = this.v0.y + j * this.ddy;
            let xArray: NVector3[] = [];
            for (let i = 0; i < this.rx; ++i) {
                let x = this.v0.x + i * this.ddx;
                const v = new NVector3(x, y, 0);
                xArray.push(v);
                this.ptList.push(v);
            }
            this.pts.push(xArray);
        }
    }
    public ComputeRec() {
        this.listPixels = [];
        for (let j = 0; j < this.ry; j += 1) {
            for (let i = 0; i < this.rx; i += 1) {
                if (i < this.rx - 1 && j < this.ry - 1) {
                    let p = new Pixel(this);
                    p.recVec.push(this.pts[j][i]);
                    p.recVec.push(this.pts[j][i + 1]);
                    p.recVec.push(this.pts[j + 1][i + 1]);
                    p.recVec.push(this.pts[j + 1][i]);
                    this.listPixels.push(p);
                }
            }
        }
    }
    public ComputeTriA() {
        this.listPixels = [];
        for (let j = 0; j < this.ry; j += 1) {
            for (let i = 0; i < this.rx; i += 1) {
                if (i < this.rx - 1 && j < this.ry - 1) {
                    let p = new Pixel(this);
                    p.recVec.push(this.pts[j][i]);
                    p.recVec.push(this.pts[j][i + 1]);
                    p.recVec.push(this.pts[j + 1][i]);
                    this.listPixels.push(p);

                    p = new Pixel(this);
                    p.recVec.push(this.pts[j][i + 1]);
                    p.recVec.push(this.pts[j + 1][i + 1]);
                    p.recVec.push(this.pts[j + 1][i]);
                    this.listPixels.push(p);
                }
            }
        }
    }
    public ComputeDia() {
        this.listPixels = [];
        for (let j = 0; j < this.ry; j += 1) {
            for (let i = 0; i < this.rx; i += 1) {
                if (i < this.rx - 2 && j < this.ry - 1) {
                    let p = new Pixel(this);
                    p.recVec.push(this.pts[j][i]);
                    p.recVec.push(this.pts[j][i + 1]);
                    p.recVec.push(this.pts[j + 1][i + 2]);
                    p.recVec.push(this.pts[j + 1][i + 1]);

                    this.listPixels.push(p);
                }
            }
        }
    }
    public ComputeLShape() {
        this.listPixels = [];
        for (let j = 0; j < this.ry; j += 3) {
            for (let i = 0; i < this.rx; i += 2) {
                if (i < this.rx - 2 && j < this.ry - 2) {
                    let p = new Pixel(this);
                    p.recVec.push(this.pts[j][i]);
                    p.recVec.push(this.pts[j][i + 2]);
                    p.recVec.push(this.pts[j + 1][i + 2]);
                    p.recVec.push(this.pts[j + 1][i + 1]);
                    p.recVec.push(this.pts[j + 2][i + 1]);
                    p.recVec.push(this.pts[j + 2][i]);
                    this.listPixels.push(p);

                    p = new Pixel(this);
                    p.recVec.push(this.pts[j + 2][i]);
                    p.recVec.push(this.pts[j + 3][i]);
                    p.recVec.push(this.pts[j + 3][i + 2]);
                    p.recVec.push(this.pts[j + 1][i + 2]);
                    p.recVec.push(this.pts[j + 1][i + 1]);
                    p.recVec.push(this.pts[j + 2][i + 1]);
                    // p.recVec.push(this.pts[j + 1][i + 2]);
                    // p.recVec.push(this.pts[j + 1][i + 1]);
                    // p.recVec.push(this.pts[j + 2][i + 1]);
                    // p.recVec.push(this.pts[j + 2][i]);
                    this.listPixels.push(p);
                }
            }
        }
    }
    public ComputeTriB() {
        this.listPixels = [];
        for (let j = 0; j < this.ry; j += 1) {
            for (let i = 0; i < this.rx; i += 1) {
                if (i < this.rx - 1 && j < this.ry - 1) {
                    let p = new Pixel(this);
                    p.recVec.push(this.pts[j][i]);
                    p.recVec.push(this.pts[j][i + 1]);
                    p.recVec.push(this.pts[j + 1][i + 1]);
                    this.listPixels.push(p);

                    p = new Pixel(this);
                    p.recVec.push(this.pts[j][i]);
                    p.recVec.push(this.pts[j + 1][i + 1]);
                    p.recVec.push(this.pts[j + 1][i]);
                    this.listPixels.push(p);
                }
            }
        }
    }
    public ComputeHex() {
        this.listPixels = [];
        let alter = true;
        for (let j = 0; j < this.ry; j += 2) {
            for (let i = 0; i < this.rx; i += 2) {
                if (alter) {
                    if (i % 2 === 0 && i < this.rx - 2 && j % 2 === 0 && j < this.ry - 2) {
                        let p = new Pixel(this);
                        p.recVec.push(this.pts[j][i + 1]);
                        p.recVec.push(this.pts[j + 1][i + 2]);
                        p.recVec.push(this.pts[j + 2][i + 2]);
                        p.recVec.push(this.pts[j + 3][i + 1]);
                        p.recVec.push(this.pts[j + 2][i]);
                        p.recVec.push(this.pts[j + 1][i]);
                        // p.recVec.push(this.pts[j][i+1]);
                        this.listPixels.push(p);
                    }
                } else {
                    if (i % 2 === 0 && i < this.rx - 2 && j % 2 === 0 && j < this.ry - 2) {
                        let p = new Pixel(this);
                        p.recVec.push(this.pts[j][i + 2]);
                        p.recVec.push(this.pts[j + 1][i + 3]);
                        p.recVec.push(this.pts[j + 2][i + 3]);
                        p.recVec.push(this.pts[j + 3][i + 2]);
                        p.recVec.push(this.pts[j + 2][i + 1]);
                        p.recVec.push(this.pts[j + 1][i + 1]);
                        // p.vecs.push(this.pts[j][i+2]);
                        this.listPixels.push(p);
                    }
                }
            }
            alter = !alter;
        }
        // console.log('done for grid');
        // this.InitGridNeighbor();
    }
    RenderPixel(ctx: CanvasRenderingContext2D) {
        for (let i = 0; i < this.listPixels.length; ++i) {
            this.listPixels[i].Render(ctx);
        }
    }
    RenderCorner(ctx: CanvasRenderingContext2D) {
        let p = this.pts[0][0];
        CanvasDrawing.PointByNVecAni(ctx, p, 7, this.t, 'rgba(255, 0, 0, 1)');
        ctx.stroke();
        p = this.pts[0][this.pts[0].length - 1];
        CanvasDrawing.PointByNVecAni(ctx, p, 7, this.t, 'rgba(255, 0, 0, 1)');
        ctx.stroke();
        p = this.pts[this.pts.length - 1][this.pts[0].length - 1];
        CanvasDrawing.PointByNVecAni(ctx, p, 7, this.t, 'rgba(255, 0, 0, 1)');
        ctx.stroke();
        p = this.pts[this.pts.length - 1][0];
        CanvasDrawing.PointByNVecAni(ctx, p, 7, this.t, 'rgba(255, 0, 0, 1)');
        ctx.stroke();
        this.t += 0.07;
    }
    public RenderSelPixel(ctx: CanvasRenderingContext2D) {
        if (this.selPixel) {
            ctx.lineWidth = 3;
            ctx.strokeStyle = '#ffffff';
            // ctx.fillStyle = `rgba(255, 0, 255, ${this.data['num'] * 0.05} )`;
            ctx.beginPath();
            ctx.moveTo(this.selPixel.recVec[0].x, this.selPixel.recVec[0].y);
            for (let i = 1; i < this.selPixel.recVec.length; ++i) {
                ctx.lineTo(this.selPixel.recVec[i].x, this.selPixel.recVec[i].y);
            }
            ctx.closePath();

            ctx.stroke();
        }
    }
}


export class Pixel {
    static ID = 0;

    map: CustomDynamicBinSystem;
    vec: NVector3;
    norV: NVector3;
    nPix: Pixel[] = [];
    recVec: NVector3[];
    i: number = -1;
    j: number = -1;
    k: number = -1;
    u: number = -1;
    v: number = -1;
    id: number;
    color: number[] = [];
    data: {};

    static GetID() { return Pixel.ID++; }

    constructor(map: CustomDynamicBinSystem, v: NVector3 = new NVector3(0, 0, 0)) {
        this.map = map;
        this.vec = v;
        this.nPix = [];
        this.recVec = [];
        this.i = -1;
        this.j = -1;
        this.k = -1;
        this.id = Pixel.GetID();
        this.data = {};
    }
    ToString() {
        let temp = 'Pixel' + this.id;
        // console.log(temp);
        return temp;
    }
    Update() {
        /* */
    }
    Render(ctx: CanvasRenderingContext2D) {
        ctx.lineWidth = 1;
        ctx.strokeStyle = '#000000';
        ctx.fillStyle = `rgba(255, 0, 255, ${this.data['num'] * 0.05} )`;
        ctx.beginPath();
        ctx.moveTo(this.recVec[0].x, this.recVec[0].y);
        for (let i = 1; i < this.recVec.length; ++i) {
            ctx.lineTo(this.recVec[i].x, this.recVec[i].y);
        }
        ctx.closePath();
        ctx.fill();
        ctx.stroke();
        // }

    }
}