    sliderD.type = 'range';
    sliderD.min = '0.001';
    sliderD.max = '10.0';
    sliderD.step = '0.0001';
    sliderD.oninput = () => {
        const val = +sliderD.value as number;
        this.interval = val;
        // console.log('this.fluid.visScale', this.fluid.visScale);
    };
    div5.appendChild(sliderD);

