  public init() {
    // TODO
  }
  public RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {

    for (let y = 0; y < ctx.canvas.height; y += this.res) {
      for (let x = 0; x < ctx.canvas.width; x += this.res) {
        // const x = j;
        // const z = i;
        // const a = this.t

        // var value = Math.round(Math.abs(simplex3(x/100, y/100, this.t*0.01))*10)/10;
        var value = Math.round(Math.abs(simplex3(x / this.scale, y / this.scale, this.t * 0.01)) * 10) / 10;
        const color = ('hsl(' + (180 - value * 180) + ',50%,50%)');
        // console.log(color);
        // const y = value*18;
        // console.log(x, y, z, this.t);
        // const value = Math.abs(simplex3(x * 0.1, y * 0.1, this.t*0.1));

        // const col = ColorUtility.HSL2RGB(value, 0.5, 0.5);

        // console.log(value, col);

        ctx.beginPath();
        ctx.rect(x, y, this.res, this.res);
        ctx.closePath();
        ctx.fillStyle = color;
        ctx.fill();

      }

    }

    this.t += this.interval;
    // console.log(this.t);
  }
