export class BoidSystem01 extends DefinitionIndeCanvasCore {
    mouse: number[] = [0, 0];

    BoidContoller1: BoidContoller1;

    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.indeCanvas.title = 'BoidSystem01';
        this.init();
    }
    init() {
        let path = BPath.GetRandomPath(0, 0, this.width, this.height);
        // let v = new NVector3(this.width * 0.5, this.height * 0.5, 0);
        this.BoidContoller1 = new BoidContoller1(path);
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#eeeeee';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        this.BoidContoller1.Update();
        this.BoidContoller1.Render(ctx);
        this.Circle(ctx);
    }
    MouseMove(x: number, y: number) {
        this.mouse = [x, y];
    }
    MouseDown() {
        this.BoidContoller1.AddAgent(this.mouse[0], this.mouse[1]);
        // if (this.lines.length > 5) { this.lines.shift(); }
        // const p1 = this.lines[this.lines.length - 1].p1;
        // this.lines.push(new Line(p1[0], p1[1], this.mouse[0], this.mouse[1] ));
    }
    Circle(ctx: CanvasRenderingContext2D) {
        let radius = 50;

        // for (let i = 0; i < 360; i += 10) {
        //     let x = Math.cos( MathUtility.Radians(i) ) + r  ;
        //     let y = Math.sin( MathUtility.Radians(i) ) + r  ;
        //     // let z =
        //     console.log(x, y);
        //     ctx.beginPath();
        //     ctx.arc(x , y, 2, 0, 3.14 * 2.0  );
        //     ctx.closePath();
        //     ctx.fill();
        // }

        for (let x = 0; x < 3.14 * 2; x += 0.5) {
            for (let y = 0; y < 3.14; y += 0.5) {
                let xx = radius * Math.cos(x) * Math.sin(y);
                let yy = radius * Math.sin(x) * Math.sin(y);
                // let zz = radius * Math.cos(y);
                // pts.push(xx, yy, zz);
                ctx.beginPath();
                ctx.arc(xx, yy, 2, 0, 3.14 * 2.0);
                ctx.closePath();
                ctx.fill();
            }
        }
    }
}