
import { NVector3 } from '../../../../../../lib';

export class DNode {

    velocity: NVector3 = new NVector3(0.0, 0.0, 0.0);
    force: NVector3 = new NVector3(0.0, 0.01, 0.0);
    gravity: NVector3 = new NVector3(0, 0.95, 0);

    v: NVector3 = new NVector3(0, 0, 0);
    r: number = 10.;
    m: number = 0.1;

    constructor(x: number, y: number, z: number, mass: number = 1.0) {
        this.v = new NVector3(x, y, 0);
        this.m = mass;
    }
    Update(width: number, height: number, dt: number, damping: number) {
        // this.velocity = new NVector3(0.0, 0.0, 0.0);
        this.velocity.Add(this.gravity);
        this.velocity.Mult(damping);
        this.force.Mult(dt / this.m);
        this.velocity.Add(this.force);

        this.v.x += this.velocity.x * dt;
        this.v.y += this.velocity.y * dt;
        this.v.z += this.velocity.z * dt;

        // console.log(this.velocity.x, this.velocity.y);
        // this.v.Add(this.velocity); // Add velocity to the location.
        // this.velocity.Add(this.gravity); // Add gravity to velocity

        // this.velocity.y += 0.95;

        this.UpdateEdge(width, height);
    }
    UpdateEdge(width: number, height: number) {
        if ((this.v.x > width) || (this.v.x < 0)) { // Bounce off edges
            this.velocity.x = this.velocity.x * -1;
        }
        if (this.v.y > height) {
            // We're reducing this.velocity ever so slightly
            // when it hits the bottom of the window
            this.velocity.y = this.velocity.y * -0.95;
            this.v.y = height;
        }
    }
    Render(ctx: CanvasRenderingContext2D) {
        ctx.beginPath();
        ctx.arc(this.v.x, this.v.y, this.r, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();
    }
}