import { DNode } from './Core/DNode';

import { Dynamic01 } from '../Dynamic01';
import { MBox } from './Matter/MBox';
import * as Matter from 'matter-js';
// https://github.com/liabru/matter-js
// https://brm.io/matter-js/

export class DynamicSystem01 {
    Dynamic01: Dynamic01;

    dNode: DNode;

    t: number = 0.0;
    engine: Matter.Engine;
    world: Matter.World;

    MBoxes: MBox[] = [];

    ground: Matter.Body;

    constructor(Dynamic01: Dynamic01) {
        this.Dynamic01 = Dynamic01;
        this.dNode = new DNode(100, 100, 0);
        this.InitMatter();
    }
    public InitMatter() {

        this.engine = Matter.Engine.create();
        this.world = this.engine.world;
        Matter.Engine.run(this.engine);

        this.MBoxes.push(new MBox(this, 0, 400, 500, 50, true)); // ground
        this.MBoxes.push(new MBox(this, 100, 100, 50, 50));
        setInterval(() => {
            this.MouseDown(100 + ((Math.random() - 0.5) * 10), 0);
        }, 1000);
    }
    public Update() {
        this.dNode.Update(this.Dynamic01.width, this.Dynamic01.height, this.t, 0.9);
        this.t += 0.1;
    }
    public MouseDown(x: number, y: number) {
        this.MBoxes.push(new MBox(this, x, y, Math.random() * 100, Math.random() * 100));
    }
    public MouseDrag(x: number, y: number) {
        this.MBoxes.push(new MBox(this, x, y, Math.random() * 100, Math.random() * 100));
    }
    public Render(ctx: CanvasRenderingContext2D) {
        this.dNode.Render(ctx);



        for (let i = 0; i < this.MBoxes.length; ++i) {
            this.MBoxes[i].Render(ctx);
        }

    }
}