import { DefinitionIndeCanvasCore } from '../../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '../..';

import {
    GetGrid, GridSystem,
    CanvasDrawing, NVector3
} from '../../../../../../../NJSCore';

// import { DynamicSystem01 } from './DynamicSystem/DynamicSystem01';

export class GridOptiA extends DefinitionIndeCanvasCore {
    mouse: NVector3 = new NVector3(0, 0);

    gridSystem: GridSystem;
    xNum: number = 10;
    yNum: number = 10;
    mode: number = 0;
    connectivity: boolean = false;
    distance: number = 0;
    // DynamicSystem: DynamicSystem01;

    p0: NVector3 = new NVector3(-1, -1);
    p1: NVector3 = new NVector3(1, 1);

    capture: NVector3 | undefined = undefined;

    titleA: HTMLElement;
    titleB: HTMLElement;
    titleC: HTMLElement;
    titleD: HTMLElement;
    titleE: HTMLElement;
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.indeCanvas.title = 'GridOptiA';
        // console.log('init GridOptiA');
        this.init();
        this.InitUI();
    }
    public InitUI() {
        const div = document.createElement('div');
        div.style.position = 'absolute';
        div.style.left = '650px';
        div.style.height = '400px';
        div.style.width = '200px';
        div.style.marginTop = '10px';
        div.style.paddingLeft = '10px';
        div.style.background = '#aaaaaa';

        this.titleA = document.createElement('p');
        this.titleA.textContent = 'X axis: 20';
        div.append(this.titleA);

        const sliderA = document.createElement('input');
        sliderA.type = 'range';
        sliderA.min = '2';
        sliderA.max = '20';
        sliderA.oninput = () => {
            const val = +sliderA.value as number;
            this.xNum = val;
            this.titleA.textContent = 'X axis: ' + val;
            this.init();
        };
        div.append(sliderA);

        this.titleB = document.createElement('p');
        this.titleB.textContent = 'Y axis: 20';
        div.append(this.titleB);

        const sliderB = document.createElement('input');
        sliderB.type = 'range';
        sliderB.min = '2';
        sliderB.max = '20';
        sliderB.oninput = () => {
            const val = +sliderB.value as number;
            this.yNum = val;
            this.titleB.textContent = 'Y axis: ' + val;
            this.init();
        };
        div.append(sliderB);

        this.titleC = document.createElement('p');
        this.titleC.textContent = 'mode: 0';
        div.append(this.titleC);

        const sliderC = document.createElement('input');
        sliderC.type = 'range';
        sliderC.min = '0';
        sliderC.max = '8';
        sliderC.defaultValue = '0';
        sliderC.oninput = () => {
            const val = +sliderC.value as number;
            // console.log(val);
            this.mode = val;
            this.titleC.textContent = 'mode: ' + val;
            this.init();
        };
        div.append(sliderC);

        this.titleD = document.createElement('p');
        this.titleD.textContent = 'connectivity: false';
        div.append(this.titleD);

        const sliderD = document.createElement('input');
        sliderD.type = 'checkbox';
        // sliderD.min = '0';
        // sliderD.max = '8';
        // sliderD.defaultValue = 'false';
        sliderD.oninput = () => {
            const val = sliderD.checked;
            // console.log(val);
            this.connectivity = val;
            this.titleD.textContent = 'connectivity: ' + val;
            this.init();
        };
        div.append(sliderD);

        this.titleE = document.createElement('p');
        this.titleE.textContent = 'distance: 0';
        div.append(this.titleE);

        const sliderE = document.createElement('input');
        sliderE.type = 'range';
        sliderE.min = '0';
        sliderE.max = '2000';
        sliderE.defaultValue = '0';
        sliderE.oninput = () => {
            const val = +sliderE.value as number;
            // console.log(val * 0.001);
            this.distance = val * 0.001;
            this.titleE.textContent = 'distance: ' + val;
            this.init();
        };
        div.append(sliderE);

        this.scene.canvas.hostDiv.append(div);
    }
    public init() {
        this.gridSystem = GetGrid.GetGrid(this.xNum, this.yNum, this.p0.x, this.p0.y, this.p1.x, this.p1.y, this.mode);
    }
    public RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.save();
        ctx.translate(this.width * 0.5, this.height * 0.5);
        ctx.scale(100, 100);

        ctx.beginPath();
        ctx.fillStyle = '#ededed';
        ctx.rect(-this.width * 0.5, -this.height * 0.5, this.width, this.height);
        ctx.fill();

        this.gridSystem.Update();

        this.RenderLine(ctx);
        this.RenderPoints(ctx);
    }
    public RenderLine(ctx: CanvasRenderingContext2D) {
        if (this.connectivity) {
            for (let i = 0; i < this.gridSystem.vs.length - 1; ++i) {
                for (let j = i + 1; j < this.gridSystem.vs.length; ++j) {
                    if (this.gridSystem.vs[j].Distance(this.gridSystem.vs[i]) < this.distance) {
                        CanvasDrawing.LineByVecs(ctx, this.gridSystem.vs[i], this.gridSystem.vs[j], 0.002, 'rgba(0, 0, 255, 0.9)');
                    }
                }
            }
        }
    }
    public RenderPoints(ctx: CanvasRenderingContext2D) {
        CanvasDrawing.PointByNVec(ctx, this.gridSystem.v0, 0.05);
        CanvasDrawing.PointByNVec(ctx, this.gridSystem.v1, 0.05);

        for (let i = 0; i < this.gridSystem.vs.length; ++i) {
            CanvasDrawing.PointByNVec(ctx, this.gridSystem.vs[i], 0.01);
        }
        // console.log(this.gridSystem.vs);
        CanvasDrawing.Point(ctx, this.mouse.x, this.mouse.y, 0.01);
        ctx.restore();
    }
    MouseMove(x: number, y: number) {
        // let xr = ((x - this.scene.canvas.canvas.offsetLeft) - this.scene.canvas.canvas.width * 0.5) * 0.01;
        // let yr = ((y - this.scene.canvas.canvas.offsetTop) - this.scene.canvas.canvas.height * 0.5) * 0.01;

        // let xr = x * 0.01;
        // let yr = y * 0.01;

        let xr = (x - this.width * 0.5) * 0.01;
        let yr = (y - this.height * 0.5) * 0.01;

        // console.log(this.width * 0.5);
        // xr += this.width * 0.5;
        // yr += this.scene.canvas.canvas.height * 0.5;
        // const xr = (x + (this.width * 0.5)) * 0.01;
        // const yr = (y + (this.height * 0.5)) * 0.01;

        this.mouse.x = xr;
        this.mouse.y = yr;


        // console.log(this.mouse);
    }
    MouseDown(x: number, y: number) {
        if (this.mouse.Distance(this.p0) < 0.1) {
            // this.p0.x = xr;
            // this.p0.y = yr;
            this.capture = this.p0;
            return;
        }
        if (this.mouse.Distance(this.p1) < 0.1) {
            // this.p1.x = xr;
            // this.p1.y = yr;
            this.capture = this.p1;
            return;
        }
        this.capture = undefined;
    }
    MouseDrag(x: number, y: number) {
        let xr = (x - this.width * 0.5) * 0.01;
        let yr = (y - this.height * 0.5) * 0.01;

        this.mouse.x = xr;
        this.mouse.y = yr;

        if (this.capture) {
            this.capture.x = xr;
            this.capture.y = yr;
            this.init();
        }
    }

}
