import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class CircleGrid extends DefinitionIndeCanvasCore {

    mouse: number[] = [0, 0];
    pts: number[][];

    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.init();
    }
    init() {
        this.mouse = [0, 0];
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.pts = [];
        let xInterval = 40;
        let yInterval = 40;

        let xDis = this.width / xInterval;
        let yDis = this.height / yInterval;

        for (let j = 0; j < yInterval; j += 1) {
            for (let i = 0; i < xInterval; i += 1) {
                this.pts.push([i * xDis, j * yDis]);
            }
        }

        let i = 0;
        for (let p of this.pts) {
            if (i % 2 === 0) {
                let col = [50, 50, 50, 1];
                let dis = this.Distance(p[0], p[1], this.mouse[0], this.mouse[1]);
                col[0] = dis;
                ctx.strokeStyle = `rgba(${col[0]},${col[1]},${col[2]},${col[3]})`;

                ctx.beginPath();
                ctx.arc(p[0], p[1], dis / 10, 0, 3.14 * 2);
                ctx.stroke();
            }
            i++;
        }

    }
    Distance(x0: number, y0: number, x1: number, y1: number): number {
        return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1));
    }
    MouseMove(x: number, y: number) {
        this.mouse = [x, y];
    }

}