import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class DefinitionLine extends DefinitionIndeCanvasCore {
    mouse: number[] = [0, 0];

    lines: Line[] = [];

    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.indeCanvas.title = 'Line';
        this.init();
    }
    init() {
        this.lines.push(new Line(10, 10, 50, 50));
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#eeeeee';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        for (let l of this.lines) {
            l.Render(ctx);
        }
    }
    MouseMove(x: number, y: number) {
        this.mouse = [x, y];
    }
    MouseDown() {
        const p1 = this.lines[this.lines.length - 1].p1;
        this.lines.push(new Line(p1[0], p1[1], this.mouse[0], this.mouse[1]));
    }

}

class Line {
    p0: number[];
    p1: number[];
    constructor(x0: number, y0: number, x1: number, y1: number) {
        this.p0 = [x0, y0];
        this.p1 = [x1, y1];
    }
    Render(ctx: CanvasRenderingContext2D) {
        ctx.strokeStyle = 'red';
        ctx.beginPath();
        ctx.moveTo(this.p0[0], this.p0[1]);
        ctx.lineTo(this.p1[0], this.p1[1]);
        ctx.closePath();
        ctx.stroke();
    }
}