import { DefinitionIndeCanvasCore } from '../../Core/DefinitionIndeCanvasCore';
import { SceneCreativeCoding } from '../..';
import { Fluid } from './Core/Fluid';

export class FluidSimulation extends DefinitionIndeCanvasCore {

  public divText: HTMLElement;

  public mouse: number[];
  public pts: number[][];

  public fluid: Fluid;

  public t = 0.0;

  public vecs: number[][] = [];

  public emmiters: number[][] = [];
  public xx = 1;
  public yy = 1;

  public img: HTMLImageElement;

  constructor(SceneCreativeCoding: SceneCreativeCoding) {
      super(SceneCreativeCoding);
      this.init();
      this.initUI();

      const img = new Image();
      // img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint_01.jpg';
      // img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint.png';
      // img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint.jpg';
      // img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint_02.jpg';
      // img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint_03.jpg';
      // img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint_04.jpg';
      // img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/Boston.jpg'
      img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/Boston_02.jpg';
      img.setAttribute('crossOrigin', '');
      // this.indeCanvas.bodyDiv.divHost.appendChild(img);

      img.onload = () => {
        this.img = img;

        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d') as CanvasRenderingContext2D;

        if (ctx) {
          ctx.drawImage(img, 0, 0, this.fluid.N, this.fluid.N);

          const data: ImageData | undefined = ctx.getImageData(0, 0, this.fluid.N, this.fluid.N);
          // const width = ctx.canvas.width;

          if (data) {

            for (let y = 0; y < this.fluid.N; ++y) {
              for (let x = 0; x < this.fluid.N; ++x) {
                const d = data.data[ (x + (this.fluid.N * y)) * 4 ];
                if (d < 200) {

                  this.fluid.cellFixed.push([x, y]);
                }

              }
            }

          }

        }

      };

  }
  public initUI() {
    let divHost = document.createElement('div');
    divHost.style.position = 'absolute';
    divHost.style.left = '450px';
    divHost.style.height = '50px';
    this.indeCanvas.bodyDiv.divHost.appendChild(divHost);

    let divBtn = document.createElement('button');
    divBtn.textContent = 'Reset';
    divBtn.style.width = '60px';
    divBtn.style.height = '20px';
    divBtn.onclick = () => {
      this.init();
    };
    divHost.appendChild(divBtn);

    let div5 = document.createElement('div');
    div5.innerHTML = 'time';
    div5.style.height = '50px';
    div5.style.width = '100px';
    divHost.appendChild(div5);

    let sliderD = document.createElement('input');
    sliderD.type = 'range';
    sliderD.min = '0.0001';
    sliderD.max = '0.7';
    sliderD.step = '0.0001';
    sliderD.oninput = () => {
        const val = +sliderD.value as number;
        this.fluid.dt = val;
        console.log('this.fluid.visScale', this.fluid.visScale);
    };
    div5.appendChild(sliderD);

    let div = document.createElement('div');
    div.innerHTML = 'Vis Scale';
    div.style.height = '50px';
    div.style.width = '100px';
    divHost.appendChild(div);

    let sliderA = document.createElement('input');
    sliderA.type = 'range';
    sliderA.min = '0.000001';
    sliderA.max = '0.8';
    sliderA.step = '0.00001';
    sliderA.oninput = () => {
        const val = +sliderA.value as number;
        this.fluid.visScale = val;
        console.log('this.fluid.visScale', this.fluid.visScale);
    };
    div.appendChild(sliderA);

    const div2 = document.createElement('div');
    div2.innerHTML = 'Viscosity';
    // div2.style.backgroundColor = '#ff0000';
    div2.style.height = '50px';
    div2.style.width = '100px';

    divHost.appendChild(div2);

    const sliderB = document.createElement('input');
    sliderB.type = 'range';
    sliderB.min = '0.000000';
    sliderB.max = '0.0001';
    sliderB.step = '0.0000001';
    sliderB.oninput = () => {
        const val = +sliderB.value as number;
        this.fluid.visc = val;
        console.log('this.fluid.visScale', this.fluid.visScale);
    };
    div2.appendChild(sliderB);

    const div3 = document.createElement('div');
    div3.innerHTML = 'rotation';
    // div3.style.backgroundColor = '#ff0000';
    div3.style.height = '50px';
    div3.style.width = '100px';

    divHost.appendChild(div3);

    const sliderC = document.createElement('input');
    sliderC.type = 'range';
    sliderC.min = '0.000000';
    sliderC.max = '360';
    sliderC.step = '0.0000001';
    sliderC.oninput = () => {
        const val = +sliderC.value as number;
        this.rotation(val);
        console.log('val', val);
    };
    div3.appendChild(sliderC);

    const divText = document.createElement('div');
    divText.innerHTML = 'rotation';
    // divText.style.backgroundColor = '#ff0000';
    divText.style.height = '50px';
    divText.style.width = '100px';
    this.divText = divText;
    divHost.appendChild(divText);

    const divRefer = document.createElement('div');
    divRefer.innerHTML = 'Ref:<br>Fluid Simulation <br>By Daniel Shiffman';
    // divRefer.style.backgroundColor = '#ff0000';
    divRefer.style.height = '50px';
    divRefer.style.width = '300px';
    divRefer.style.fontSize = '14px';
    divHost.appendChild(divRefer);

    this.emmiters.push([Math.floor( 0.5 * this.fluid.N), Math.floor( 0.5 * this.fluid.N)]);
  }
  public init() {
      this.fluid = new Fluid(0.02, 0, 0.0000001);
      this.emmiters = [];
      this.emmiters.push([Math.floor( 0.5 * this.fluid.N), Math.floor( 0.5 * this.fluid.N)]);
  }
  public RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {

      // console.log('cx, cy', cx, cy)

      for (let k = 0 ; k < this.emmiters.length; ++k) {
        let cx = this.emmiters[k][0];
        let cy = this.emmiters[k][1];
        for (let i = -1; i <= 1; i++) {
          for (let j = -1; j <= 1; j++) {
            // this.fluid.addDensity(cx + i, cy + j, random(50, 150));
            // console.log((Math.random() * 100) + 50)
            this.fluid.addDensity(cx + i, cy + j, (Math.random() * 5) );
          }
        }
      }

      for (let k = 0 ; k < this.emmiters.length; ++k) {
        let cx = this.emmiters[k][0];
        let cy = this.emmiters[k][1];
        for (let i = 0; i < 2; i++) {
          // let angle = noise(t) * Math.PI * 2;
          // let v = p5.Vector.fromAngle(angle);
          // v.mult(0.2);
          // this.t += 0.01;
          // this.fluid.addVelocity(cx, cy, v.x, v.y);
          this.fluid.addVelocity(cx, cy, this.xx, this.yy);
          // this.fluid.addVelocity(cx, cy, 1 , 1);
        }
      }

      this.fluid.step();
      this.fluid.renderD(ctx);
      // this.fluid.renderV(ctx);

      // if (this.img) {
      //   ctx.drawImage(this.img, 0, 0);
      // }
  }
  public MouseLeftClick(x: number, y: number) {
    const xx = Math.floor(x / this.fluid.SCALE);
    const yy = Math.floor(y / this.fluid.SCALE);
    this.emmiters.push([xx, yy]);
  }
  public MouseDrag(x: number, y: number) {
    const xx = Math.floor(x / this.fluid.SCALE);
    const yy = Math.floor(y / this.fluid.SCALE);
    this.fluid.cellFixed.push([xx, yy]);
  }
  public rotation(angle: number) {
    const theta = angle * Math.PI / 180;
    this.xx = Math.cos(theta);
    this.yy = Math.sin(theta);
  }
  public MouseMove(x: number, y: number) {
    const xx = Math.floor(x / this.fluid.SCALE);
    const yy = Math.floor(y / this.fluid.SCALE);
    const d = this.fluid.density[xx + yy * this.fluid.N];
    if (d) {
      this.divText.innerHTML = `${d}`;
    }
  }
}