import { Bodies, World, Vector } from 'matter-js';
import { Scene } from '../Scene';
import { MGeometryBase } from './Base/MGeometryBase';
export class MBox extends MGeometryBase {
    body: Matter.Body;
    scene: Scene;
    angle: number;
    position: Vector;
    width: number;
    height: number;
    constructor(scene: Scene, x: number, y: number, width: number, height: number, isStatic: boolean = false) {
        super(scene);
        this.width = width;
        this.height = height;
        const restitution = 0.5;
        const friction = 0.5;
        this.body = Bodies.rectangle(x, y, width, height, { isStatic: isStatic, restitution: restitution, friction: friction });
        World.add(this.scene.world, this.body);
    }
    public Render(ctx: CanvasRenderingContext2D) {
        this.angle = this.body.angle;
        this.position = this.body.position;

        this.DrawRectByCen(ctx, this.position.x, this.position.y, this.width, this.height, this.angle);
    }
    public DrawRectByCen(ctx: CanvasRenderingContext2D, x: number, y: number, width: number, height: number, angle: number) {
        const xOff = width * 0.5;
        const yOff = height * 0.5;
        ctx.save();
        ctx.translate(this.position.x, this.position.y);
        ctx.rotate(angle);
        ctx.beginPath();
        ctx.rect(-xOff, -yOff, this.width, this.height);
        ctx.fill();
        ctx.restore();
    }
}