import { NVector3, MathUtility } from '../../../../../lib';

export class BoidContollerWander0 {

  boids: Boid[] = [];

  constructor() {
    this.boids = [];
  }
  public AddAgent(x: number, y: number, w: number, h: number) {
    let boid = new Boid(new NVector3(x, y), new NVector3(0, 0), new NVector3(w, h));
    this.boids.push(boid);
  }
  public Update() {
    for (let b of this.boids) {
      b.Update();
    }

  }
  public GetPoint() {
    let pts: NVector3[] = [];
    for (let b of this.boids) {
      if (b.isRenderable) {
        pts.push(b.p);
      }
    }
    return pts;
  }

  // .......................................................................................................................

  Render(ctx: CanvasRenderingContext2D) {
    this.boids.forEach((b) => {
      b.Render(ctx);
    });
  }
}

export class Boid {

  static list: Boid[] = [];

  maxForce = 2; // 1; // 5;
  maxVelocity = 2; // 1; // 8;
  maxSpeed = 0.9; // 1; // 13.0;
  mass = 100.2;

  isRenderable = true;

  p: NVector3;
  p0: NVector3;
  p1: NVector3;
  targets: NVector3[];
  target: NVector3;
  targetID = 0;

  col: boolean = false;
  desiredVelocity: NVector3;
  steering: NVector3;
  velocity: NVector3 = new NVector3(0, 0, 0);

  // .......................
  wanderAngle: number = 0;
  CIRCLE_DISTANCE = 50;
  CIRCLE_RADIUS = 50;
  ANGLE_CHANGE = 15;

  t: number = 0.0;

  MAX_QUEUE_AHEAD = 15;
  MAX_QUEUE_RADIUS = 10;
  trajectory: NVector3[] = [];
  timerTrajectory = 0.0;

  constructor(p: NVector3, p0: NVector3, p1: NVector3) {
    Boid.list.push(this);
    this.p = p;
    this.p0 = p0;
    this.p1 = p1;
    this.UpdateTarget();
  }
  public Update() {
    // this.UpdateVec0();
    this.UpdateVec1();
    // this.UpdateVecSlowDown1();
    // this.UpdateVecSlowDown2();

  }
  UpdateTarget() {
    let x = MathUtility.GetRandomInt(this.p0.x, this.p1.x);
    let y = MathUtility.GetRandomInt(this.p0.y, this.p1.y);
    this.target = new NVector3(x, y);
  }
  UpdateVec1() {
    this.desiredVelocity = new NVector3(this.target.x - this.p.x, this.target.y - this.p.y, this.target.z - this.p.z);
    this.desiredVelocity.Unitize();
    this.desiredVelocity.x *= this.maxVelocity;
    this.desiredVelocity.y *= this.maxVelocity;
    this.desiredVelocity.z *= this.maxVelocity;

    let x = (this.desiredVelocity.x - this.velocity.x) < this.maxForce ? this.desiredVelocity.x - this.velocity.x : this.maxForce;
    let y = (this.desiredVelocity.y - this.velocity.y) < this.maxForce ? this.desiredVelocity.y - this.velocity.y : this.maxForce;
    let z = (this.desiredVelocity.z - this.velocity.z) < this.maxForce ? this.desiredVelocity.z - this.velocity.z : this.maxForce;

    this.steering = new NVector3(x / this.mass, y / this.mass, z / this.mass);

    this.velocity.x = (this.velocity.x + this.steering.x) < this.maxSpeed ? this.velocity.x + this.steering.x : this.maxSpeed;
    this.velocity.y = (this.velocity.y + this.steering.y) < this.maxSpeed ? this.velocity.y + this.steering.y : this.maxSpeed;
    this.velocity.z = (this.velocity.z + this.steering.z) < this.maxSpeed ? this.velocity.z + this.steering.z : this.maxSpeed;

    this.Queue();

    this.p.x += this.velocity.x;
    this.p.y += this.velocity.y;
    this.p.z += this.velocity.z;
    this.t += 0.01;
    if (this.t > 3) {
      this.t = 0.0;
      this.UpdateTarget();
    }
  }
  UpdateVec0() {
    // this.desiredVelocity = new NVector3(this.target.x - this.p.x, this.target.y - this.p.y, this.target.z - this.p.z);
    // this.desiredVelocity.Unitize();
    // this.desiredVelocity.x *= this.maxVelocity;
    // this.desiredVelocity.y *= this.maxVelocity;
    // this.desiredVelocity.z *= this.maxVelocity;

    // let x = (this.desiredVelocity.x - this.velocity.x) < this.maxForce ? this.desiredVelocity.x - this.velocity.x : this.maxForce;
    // let y = (this.desiredVelocity.y - this.velocity.y) < this.maxForce ? this.desiredVelocity.y - this.velocity.y : this.maxForce;
    // let z = (this.desiredVelocity.z - this.velocity.z) < this.maxForce ? this.desiredVelocity.z - this.velocity.z : this.maxForce;

    // this.steering = ;

    this.steering = this.Wander(new NVector3(this.velocity.x, this.velocity.y, this.velocity.z));

    this.steering.x = this.steering.x < this.maxForce ? this.steering.x : this.maxForce;
    this.steering.y = this.steering.y < this.maxForce ? this.steering.y : this.maxForce;
    this.steering.z = this.steering.z < this.maxForce ? this.steering.z : this.maxForce;

    this.steering.x /= this.mass;
    this.steering.y /= this.mass;
    this.steering.z /= this.mass;

    if (this.velocity.x + this.steering.x < this.maxSpeed) {
      this.velocity.x = this.velocity.x + this.steering.x;
    } else {
      this.velocity.x = this.maxSpeed;
    }
    if (this.velocity.y + this.steering.y < this.maxSpeed) {
      this.velocity.y = this.velocity.y + this.steering.y;
    } else {
      this.velocity.y = this.maxSpeed;
    }
    if (this.velocity.z + this.steering.z < this.maxSpeed) {
      this.velocity.z = this.velocity.z + this.steering.z;
    } else {
      this.velocity.z = this.maxSpeed;
    }
    // this.velocity.x = this.velocity.x + this.steering.x < this.maxSpeed ? this.velocity.x + this.steering.x : this.maxSpeed;
    // this.velocity.y = this.velocity.y + this.steering.y < this.maxSpeed ? this.velocity.y + this.steering.y : this.maxSpeed;
    // this.velocity.z = this.velocity.z + this.steering.z < this.maxSpeed ? this.velocity.z + this.steering.z : this.maxSpeed;

    // steering = wander()
    // steering = truncate (steering, max_force)
    // steering = steering / mass
    // velocity = truncate (velocity + steering , max_speed)
    // position = position + velocity
    this.Queue();

    console.log(this.maxSpeed);

    this.p.x += this.velocity.x;
    this.p.y += this.velocity.y;
    this.p.z += this.velocity.z;

  }
  Wander(velocity: NVector3) {
    // Calculate the circle center
    let circleCenter = new NVector3(velocity.x, velocity.y, velocity.z);
    circleCenter.Normalize();
    circleCenter.Scale(this.CIRCLE_DISTANCE);
    //
    // Calculate the displacement force
    let displacement = new NVector3(0, -1);
    displacement.Scale(this.CIRCLE_RADIUS);
    //
    // Randomly change the vector direction
    // by making it change its current angle
    displacement = this.SetAngle(displacement, this.wanderAngle);
    //
    // Change wanderAngle just a bit, so it
    // won't have the same value in the
    // next game frame.
    this.wanderAngle += Math.random() * this.ANGLE_CHANGE - this.ANGLE_CHANGE * .5;
    //
    // Finally calculate and return the wander force
    var wanderForce: NVector3;
    wanderForce = circleCenter.Add(displacement);
    return wanderForce;
  }
  SetAngle(v: NVector3, value: number) {
    let len: number = v.Length();
    v.x = Math.cos(value) * len;
    v.y = Math.sin(value) * len;
    return v;
  }
  GetNeighborAhead() {
    // var i:int;
    var ret: Boid | undefined = undefined;
    var qa: NVector3 = this.velocity.Clone();

    qa.Normalize();
    qa.Scale(this.MAX_QUEUE_AHEAD);

    let ahead = this.p.Clone().Add(qa);

    for (let i = 0; i < Boid.list.length; i++) {
      let neighbor: Boid = Boid.list[i];
      let d = NVector3.Distance(ahead, neighbor.p);
      if (neighbor !== this && d <= this.MAX_QUEUE_RADIUS) {
        ret = neighbor;
        break;
      }
    }

    return ret;
  }
  Queue() {
    var neighbor = this.GetNeighborAhead();

    if (neighbor !== undefined) {
      this.velocity.Scale(0.3);
    }

    return new NVector3(0, 0);
  }

  // .......................................................................................................................
  Render(ctx: CanvasRenderingContext2D) {
    // this.RenderDesiredVelocity(ctx);
    // this.RenderCurrentVelocity(ctx);
    this.RenderCircle(ctx);
  }
  RenderDesiredVelocity(ctx: CanvasRenderingContext2D) {
    // let scale = 20;
    // ctx.lineWidth = 0.5;
    // ctx.strokeStyle = '#00ff00';
    // ctx.beginPath();
    // ctx.moveTo(this.p.x, this.p.y);
    // ctx.lineTo(this.p.x + this.desiredVelocity.x * scale, this.p.y + this.desiredVelocity.y * scale);
    // ctx.closePath();
    // ctx.stroke();
  }
  RenderCurrentVelocity(ctx: CanvasRenderingContext2D) {
    // let scale = 20;
    // ctx.lineWidth = 0.5;
    // ctx.strokeStyle = '#ff0000';
    // ctx.beginPath();
    // ctx.moveTo(this.p.x, this.p.y);
    // ctx.lineTo(this.p.x + this.velocity.x * scale, this.p.y + this.velocity.y * scale);
    // ctx.closePath();
    // ctx.stroke();
  }
  RenderCircle(ctx: CanvasRenderingContext2D) {
    this.timerTrajectory += 1;
    if (this.timerTrajectory % 5 === 0) {
      if (this.trajectory.length > 30) {
        this.trajectory.shift();
      }
      this.trajectory.push(new NVector3(this.p.x, this.p.y));

      this.timerTrajectory = 0;
    }
    for (let i = 0; i < this.trajectory.length; ++i) {
      ctx.fillStyle = 'rgba(100, 100, 100 ,0.1)';
      ctx.beginPath();
      ctx.arc(this.trajectory[i].x, this.trajectory[i].y, (i) * 0.2, 0, 3.14 * 2);
      ctx.closePath();
      ctx.fill();
    }
    ctx.strokeStyle = '#000000';
    ctx.lineWidth = 0.5;
    // ctx.beginPath();
    // ctx.moveTo(this.targets[0].x , this.targets[0].y);
    // for (let i = 1 ; i < this.targets.length; ++i) {
    //   ctx.lineTo(this.targets[i].x , this.targets[i].y);
    // }
    // ctx.closePath();
    // ctx.stroke();

    ctx.fillStyle = '#000000';
    ctx.beginPath();
    ctx.arc(this.p.x, this.p.y, 2.5, 0, 3.14 * 2);
    ctx.closePath();
    ctx.fill();
    ctx.beginPath();
    ctx.arc(this.p.x, this.p.y, 10, 0, 3.14 * 2);
    ctx.closePath();
    // ctx.fill();
    ctx.stroke();
  }
}

