import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class BallBounceClass extends DefinitionIndeCanvasCore {

    Balls: Ball[] = [];

    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.Balls.push(new Ball(this, 50, 50));
        this.Balls.push(new Ball(this, 100, 100));
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#eeeeee';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        for (let b of this.Balls) { b.Render(ctx); }
    }
    MouseDown(x: number, y: number) {
        this.Balls.push(new Ball(this, x, y));
    }
}

class Ball {

    xpos: number = 100;
    ypos: number = 100;

    xspeed: number = 10;
    yspeed: number = 10;

    xdirection: number = 1;
    ydirection: number = 1;

    rad: number = 50;

    BallBounceClass: BallBounceClass;

    color: string;

    static GetRandomHex() {
        return this.RGBToHex(Math.round(Math.random() * 255), Math.round(Math.random() * 255), Math.round(Math.random() * 255));
    }
    static RGBToHex(r: number, g: number, b: number) {
        return '#' + [Math.round(r), Math.round(g), Math.round(b)].map(x => {
            const hex = x.toString(16);
            return hex.length === 1 ? '0' + hex : hex;
        }).join('');
    }
    constructor(BallBounceClass: BallBounceClass, x: number, y: number) {
        this.BallBounceClass = BallBounceClass;
        this.xpos = x;
        this.ypos = y;
        this.xdirection = (Math.random() * 2) - 1;
        this.ydirection = (Math.random() * 2) - 1;
        this.rad = (Math.random() * 50) + 10;
        this.color = Ball.GetRandomHex();
    }
    Render(ctx: CanvasRenderingContext2D) {
        this.xpos = this.xpos + (this.xspeed * this.xdirection);
        this.ypos = this.ypos + (this.yspeed * this.ydirection);
        if (this.xpos > this.BallBounceClass.width - this.rad || this.xpos < this.rad) {
            this.xdirection *= -1;
        }
        if (this.ypos > this.BallBounceClass.height - this.rad || this.ypos < this.rad) {
            this.ydirection *= -1;
        }

        // Draw the shape
        ctx.fillStyle = this.color;
        ctx.beginPath();
        ctx.arc(this.xpos, this.ypos, this.rad, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();
    }
}