import { NVector3, CircularProjctionFromPoint } from '../../../../../../../../NJSCore/Core/CoreGeometry/NGeometry/NVector3';

enum PointID {
  Cp = 'cp',
  L1 = 'l1',
  L2 = 'l2',
  R1 = 'r1',
  R2 = 'r2',
}


export class ColorPicker {
  public canvasBase: HTMLCanvasElement;
  public ctxBase: CanvasRenderingContext2D;
  public canvas: HTMLCanvasElement;
  public ctx: CanvasRenderingContext2D;
  public img: HTMLImageElement;

  public cp: CPoint;
  public cpL1: CPoint;
  public cpL2: CPoint;
  public cpR1: CPoint;
  public cpR2: CPoint;

  public capture: Point | undefined = undefined;
  public r: number;
  public offset: number = 15;

  public m = new NVector3(0, 0, 0);

  constructor(div: HTMLDivElement) {
    div.style.background = '#eeeeee';

    this.canvasBase = document.createElement('canvas');
    this.canvasBase.style.position = 'absolute';
    div.append(this.canvasBase);
    this.ctxBase = this.canvasBase.getContext('2d') as CanvasRenderingContext2D;
    this.canvasBase.width = 300;
    this.canvasBase.height = 300;
    this.r = 150;
    this.ctxBase.translate(this.r, this.r);
    // this.img = new Image();
    // this.img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/color/color-wheel.png';
    // this.img.onload = () => {
    //   this.canvasBase.width = this.img.width;
    //   this.canvasBase.height = this.img.height;
    //   this.ctxBase.drawImage(this.img, 0, 0);

    //   this.canvas = document.createElement('canvas');
    //   this.canvas.style.position = 'absolute';
    //   this.canvas.width = this.canvasBase.width;
    //   this.canvas.height = this.canvasBase.height;
    //   this.ctx = this.canvas.getContext('2d') as CanvasRenderingContext2D;

    //   div.append(this.canvas);

    //   this.r = (this.canvasBase.width * 0.5) - this.offset;
    //   this.ctx.translate(this.r, this.r);
    //   this.init();
    // };

    this.canvas = document.createElement('canvas');
    this.canvas.style.position = 'absolute';
    this.canvas.width = this.canvasBase.width;
    this.canvas.height = this.canvasBase.height;
    this.ctx = this.canvas.getContext('2d') as CanvasRenderingContext2D;
    this.ctx.translate(this.r, this.r);


    div.append(this.canvas);
    this.initEvent();
    this.init();
  }
  private initEvent() {
    this.canvas.addEventListener('mousedown', e => {
      const m = this.MousePostProcess(e);
      this.mouseDown(m[0], m[1]);
    });
    this.canvas.addEventListener('mousemove', e => {
      const m = this.MousePostProcess(e);
      this.mouseMove(m[0], m[1]);
    });
    this.canvas.addEventListener('mouseup', e => {
      const m = this.MousePostProcess(e);
      this.mouseUp(m[0], m[1]);
    });
  }
  private MousePostProcess(e: MouseEvent) {
    const x = e.offsetX - this.r;
    const y = e.offsetY - this.r;

    // if (Math.sqrt(x * x + y * y) > this.r) {
    //   return [this.m.x, this.m.y];
    // }
    this.m.x = x;
    this.m.y = y;

    return [x, y];
  }

  private init() {
    this.cp = new CPoint(this, 0, 0, PointID.Cp);
    this.cpL1 = new L1Point(this, 10, 10, PointID.L1);
    this.cpL1.updatePosByRadian(3);
    this.cpR1 = new R1Point(this, 10, 10, PointID.R1);
    this.cpR1.updatePosByRadian(-3);


    let radius = this.r;
    let image = this.ctxBase.createImageData(2 * radius, 2 * radius);
    let data = image.data;

    for (let x = -radius; x < radius; x++) {
      for (let y = -radius; y < radius; y++) {
        let [r, phi] = xy2polar(x, y);

        if (r > radius) {
          // skip all (x,y) coordinates that are outside of the circle
          continue;
        }

        let deg = rad2deg(phi);

        // Figure out the starting index of this pixel in the image data array.
        let rowLength = 2 * radius;
        let adjustedX = x + radius; // convert x from [-50, 50] to [0, 100] (the coordinates of the image data array)
        let adjustedY = y + radius; // convert y from [-50, 50] to [0, 100] (the coordinates of the image data array)
        let pixelWidth = 4; // each pixel requires 4 slots in the data array
        let index = (adjustedX + adjustedY * rowLength) * pixelWidth;

        let hue = deg;
        let saturation = 1.0;
        let value = 1.0;

        let [red, green, blue] = hsv2rgb(hue, saturation, value);
        let alpha = 255;

        data[index] = red;
        data[index + 1] = green;
        data[index + 2] = blue;
        data[index + 3] = alpha;
      }
    }

    this.ctxBase.putImageData(image, 0, 0);

    this.ctxBase.beginPath();
    this.ctxBase.arc(0, 0, 1, 0, Math.PI * 2);
    this.ctxBase.closePath();
    this.ctxBase.fill();


    this.render(this.ctx);
  }
  private render(ctx: CanvasRenderingContext2D) {
    ctx.clearRect(-this.r, -this.r , this.r * 2, this.r * 2);
    this.cp.render(ctx);
    this.cpL1.render(ctx);
    this.cpR1.render(ctx);

  }
  public mouseDrag(x: number, y: number) {
    // this.cp.vec.x = x;
    // this.cp.vec.y = y;
    // console.log(x, y);
    // this.render(this.ctx);
  }
  public mouseMove(x: number, y: number) {
    if (this.capture) {
      if (this.capture.id === PointID.Cp) {
        const vec = new NVector3(x, y, 0);
        let length = vec.Length();
        length = length > this.r ? this.r : length;
        const v = CircularProjctionFromPoint(vec, length );
        this.capture.updatePos(v);
      } else if (this.capture.id === PointID.L1) {
        const vec = new NVector3(x, y, 0);
        let length = vec.Length();
        length = length > this.r ? this.r : length;
        const v = CircularProjctionFromPoint(vec, length );
        this.capture.updatePos(v);
      } else if (this.capture.id === PointID.R1) {
        const vec = new NVector3(x, y, 0);
        let length = vec.Length();
        length = length > this.r ? this.r : length;
        const v = CircularProjctionFromPoint(vec, length );
        this.capture.updatePos(v);
      }
    }

    this.render(this.ctx);
  }
  public mouseDown(x: number, y: number) {
    this.capture = this.GetPoint(x, y);
  }
  public mouseUp(x: number, y: number) {
    this.capture = undefined;
  }
  public GetPoint(x: number, y: number): Point | undefined {
    if (this.cp.getDistance(x, y) < this.cp.r) {
      return this.cp;
    }
    if (this.cpL1.getDistance(x, y) < this.cp.r) {
      return this.cpL1;
    }
    if (this.cpR1.getDistance(x, y) < this.cp.r) {
      return this.cpR1;
    }

    return undefined;
  }
}



class Point {
  colorPicker: ColorPicker;
  vec: NVector3;
  r: number = 15;
  radian: number = 0;
  radius: number = 10;
  id: PointID;
  color: string = '#ffffff';

  constructor(colorPicker: ColorPicker, x: number, y: number, id: PointID) {
    this.colorPicker = colorPicker;
    this.vec = new NVector3(x, y, 0);
    this.id = id;
  }
  public getRadian() {
    this.radian = Math.atan2(this.vec.x , this.vec.y);
    this.radian -= Math.PI * 0.5;
    this.radian *= -1;
    return this.radian;
  }
  public updatePosByRadian(radian: number) {
    // console.log(radian);
    this.vec.x = Math.cos(radian);
    this.vec.y = Math.sin(radian);
    this.vec.x *= this.radius;
    this.vec.y *= this.radius;
  }
  public updatePos(v: NVector3) {
    if (this.id === PointID.Cp) {
      this.vec.x = v.x;
      this.vec.y = v.y;
      this.radius = this.vec.Length();
      this.getRadian();

      this.colorPicker.cpL1.radius = this.radius;
      this.colorPicker.cpL1.updatePosByRadian(this.colorPicker.cpL1.radian +  this.colorPicker.cpL1.radian - this.radian );

      this.colorPicker.cpR1.radius = this.radius;
      // this.colorPicker.cpR1.updatePosByRadian(this.colorPicker.cpL1.radian -  this.colorPicker.cpL1.radian + this.radian);
    }
  }
  public updateposByCPOffset(v: number) {

  }
  public getDistance(x: number, y: number) {
    const dis = Math.sqrt((this.vec.x - x) * (this.vec.x - x) + (this.vec.y - y) * (this.vec.y - y));
    // console.log(x, y, this.vec.x, this.vec.y, dis);
    return dis;
  }
  public render(ctx: CanvasRenderingContext2D) {
    const c: ImageData = this.colorPicker.ctxBase.getImageData(this.vec.x + this.colorPicker.r, this.vec.y + this.colorPicker.r, 2, 2);
    this.color = `rgb(${c.data[0]}, ${c.data[1]}, ${c.data[2]})`;
    // console.log(this.color);

    ctx.strokeStyle = '#ffffff';
    ctx.lineWidth = 3;
    ctx.beginPath();
    ctx.moveTo(0, 0);
    ctx.lineTo(this.vec.x, this.vec.y);
    ctx.closePath();
    ctx.stroke();

    ctx.beginPath();
    ctx.arc(this.vec.x, this.vec.y, this.r, 0, Math.PI * 2);
    ctx.closePath();
    ctx.fillStyle = this.color;
    ctx.fill();
    ctx.stroke();
  }
}

class CPoint extends Point {
  constructor(colorPicker: ColorPicker, x: number, y: number, id: PointID) {
    super(colorPicker, x, y, id);
  }
}
class L1Point extends Point {

  constructor(colorPicker: ColorPicker, x: number, y: number, id: PointID) {
    super(colorPicker, x, y, id);
  }
  public updatePos(v: NVector3) {
      this.vec.x = v.x;
      this.vec.y = v.y;
      this.radius = this.vec.Length();
      this.getRadian();
      console.log(-this.radius);

      let offset = this.colorPicker.cp.radian;
      this.colorPicker.cpR1.updatePosByRadian(-this.radian + offset * 2);
  }
  // }
}


class R1Point extends Point {

  constructor(colorPicker: ColorPicker, x: number, y: number, id: PointID) {
    super(colorPicker, x, y, id);
  }
  public updatePos(v: NVector3) {
      this.vec.x = v.x;
      this.vec.y = v.y;
      this.radius = this.vec.Length();
      this.getRadian();
      console.log(-this.radius);

      let offset = this.colorPicker.cp.radian;
      this.colorPicker.cpL1.updatePosByRadian(-this.radian + offset * 2);
    }
  public updateposByCPOffset(v: number) {
      this.radian += v;
      this.vec.x = Math.cos(this.radian);
      this.vec.y = Math.sin(this.radian);
      this.vec.x *= this.radius;
      this.vec.y *= this.radius;
  }
}




const xy2polar = (x: number, y: number) => {
  let r = Math.sqrt(x * x + y * y);
  let phi = Math.atan2(y, x);
  return [r, phi];
};

// rad in [-, ] range
// return degree in [0, 360] range
const rad2deg = (rad: number) => {
  return ((rad + Math.PI) / (2 * Math.PI)) * 360;
};

// // Utility functions (ignore mostly)
const hsv2rgb = (h: number, s: number, v: number) => {
  var c = v * s;
  var h1 = h / 60;
  var x = c * (1 - Math.abs((h1 % 2) - 1));
  var m = v - c;
  var rgb: number[] = [0, 0, 0];

  if (typeof h === 'undefined') rgb = [0, 0, 0];
  else if (h1 < 1) rgb = [c, x, 0];
  else if (h1 < 2) rgb = [x, c, 0];
  else if (h1 < 3) rgb = [0, c, x];
  else if (h1 < 4) rgb = [0, x, c];
  else if (h1 < 5) rgb = [x, 0, c];
  else if (h1 <= 6) rgb = [c, 0, x];

  return [255 * (rgb[0] + m), 255 * (rgb[1] + m), 255 * (rgb[2] + m)];
};
