import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';
import { SceneCreativeCoding } from '..';
export class AlgorithmBuilding extends DefinitionIndeCanvasCore {
    p: number[] = [0, 0];
    myWork: MyWork;
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.init();

    }
    init() {
        let site = [
            [10, 10],
            [50, 15],
            [45, 65],
            [25, 80],
            [9, 10],
        ];
        this.myWork = new MyWork(10, site);
        this.myWork.toJson();
        // const json = JSON.parse(JSON.stringify(this.myWork));
        // console.log(json);
        // let after = performance.now();
        // console.log('time: ', after - before);
        console.log('building run....');
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        // this.map.Render(ctx, this.p);
        ctx.fillStyle = '#ff0000';
        ctx.beginPath();
        ctx.arc(this.p[0], this.p[1], 5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();

    }
    MouseMove(x: number, y: number) {
        this.p = [x, y];
    }
    MouseDown(x: number, y: number) {
        this.p = [x, y];
    }
}
class MyWork {
    site: Site;
    apts: APT[] = [];
    sun: Sun;
    constructor(num: number, site: number[][]) {
        this.sun = new Sun();
        this.init();
        this.InitOpti();
    }
    init() {
        this.site = new Site();
        for (let i = 0; i < 10; ++i) {
            this.apts.push(new APT());
        }
    }
    InitOpti() {
        //
    }
    toJson() {
        for (let i = 0; i < 10; ++i) {
            this.apts[i].toJson();
        }
    }
}
