import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class CircleGridAttractors1 extends DefinitionIndeCanvasCore {

    mouse: number[] = [0, 0];
    pts: number[][];

    attrs: number[][] = [[0, 0]];
    strength: number = 50;
    falloff: number = 0.00005;

    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.init();
    }
    init() {
        this.mouse = [0, 0];
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.pts = [];
        let xInterval = 60;
        let yInterval = 40;

        let xDis = this.width / xInterval;
        let yDis = this.height / yInterval;

        for (let j = 0; j < yInterval; j += 1) {
            for (let i = 0; i < xInterval; i += 1) {
                this.pts.push([i * xDis, j * yDis]);
            }
        }

        let i = 0;
        for (let p of this.pts) {
            if (i % 2 === 0) {
                let col = [50, 255, 0, 1];

                let dis = 0;
                for (let j of this.attrs) {
                    let distance = this.Attractor(p, j, this.strength, this.falloff);
                    if (dis < distance) { dis = distance; }
                }

                col[2] += dis * 3;
                ctx.strokeStyle = `rgba(${col[0]},${col[1]},${col[2]},${col[3]})`;

                ctx.beginPath();
                ctx.arc(p[0], p[1], 10 + dis, 0, 3.14 * 2);
                ctx.stroke();
            }
            i++;
        }

    }
    Attractor(pt: number[], attractor: number[], strength: number, falloff: number): number {
        let v = this.Sub(pt, attractor);
        let dist = this.Length(v);
        let result = strength * Math.exp(-falloff * dist * dist);
        return result;
    }
    Sub(v0: number[], v1: number[]): number[] {
        return [v0[0] - v1[0], v0[1] - v1[1]];
    }
    Unitize(v: number[]): number[] {
        let len = this.Length(v);
        return [v[0] / len, v[1] / len];
    }
    Length(v: number[]) {
        return Math.sqrt(v[0] * v[0] + v[1] * v[1]);
    }
    MouseMove(x: number, y: number) {
        this.attrs[this.attrs.length - 1] = [x, y];
    }
    MouseDown(x: number, y: number) {
        this.attrs.push([x, y]);
    }
    MouseWheel(x: number, y: number, v: number) {
        this.falloff += v * 0.0000001;
    }
}