import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class AlgorithmDFSBFS extends DefinitionIndeCanvasCore {
    p: number[] = [0, 0];
    graph: Graph;
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.init();

    }
    init() {
        const size = [4, 5, 1];
        const array = [
            [1, 2],
            [1, 3],
            [1, 4],
            [2, 4],
            [3, 4],
        ];
        // const size = [5, 5, 3];
        // const array = [
        //             [5, 4],
        //             [5, 2],
        //             [1, 2],
        //             [3, 4],
        //             [3, 1]
        // ];
        let before = performance.now();
        this.graph = new Graph(size, array);
        let after = performance.now();
        console.log('time: ', after - before);
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.graph.Render(ctx, this.p);
        this.RenderMouse(ctx);
    }
    RenderMouse(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#ff0000';
        ctx.beginPath();
        ctx.arc(this.p[0], this.p[1], 5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();
    }
    MouseMove(x: number, y: number) {
        this.p = [x, y];
    }
    MouseDown(x: number, y: number) {
        this.p = [x, y];
    }
}
// https://www.acmicpc.net/problem/1260
class Value {
    visitedNum: number[] = [];
    constructor() {
        //
    }
    Add(id: number) {
        this.visitedNum.push(id);
    }
    Print() {
        for (let i = 0; i < this.visitedNum.length; ++i) {
            console.log(this.visitedNum[i]);
        }
    }
}
class Graph {
    edges: Edge[] = [];
    nodes: Node[] = [];
    constructor(data: number[], connections: number[][]) {
        for (let i = 0; i < data[0]; ++i) {
            this.nodes.push(new Node(i));
        }
        for (let i = 0; i < connections.length; ++i) {
            const cons = connections[i];
            const n0 = this.nodes[cons[0] - 1];
            const n1 = this.nodes[cons[1] - 1];
            const e = new Edge(n0, n1);
            this.edges.push(e);
        }
        let val = new Value();
        this.ComputeDFS(data[data.length - 1] - 1, val);
        val.Print();

        for (let i = 0; i < this.nodes.length; ++i) { this.nodes[i].visited = false; }

        val = new Value();
        this.ComputeBFS(data[data.length - 1] - 1, val);
        val.Print();
    }
    // ...............................................................
    ComputeBFS(start: number, val: Value) {
        this.BFS(this.nodes[start], val);
    }
    private BFS(n: Node, val: Value) {
        let queue = [n];

        while (queue.length > 0) {
            let current = queue[0];
            queue.shift();
            if (current.visited) { continue; }
            current.visited = true;
            val.Add(current.id + 1);

            // for (let i = 0; i < current.edges.length; ++i) {
            //     if (!current.edges[i].n0.visited) { queue.push(current.edges[i].n0); }
            //     if (!current.edges[i].n1.visited) { queue.push(current.edges[i].n1); }
            // }
            for (let i = current.edges.length - 1; i > - 1; --i) {
                if (!current.edges[i].n0.visited) { queue.push(current.edges[i].n0); }
                if (!current.edges[i].n1.visited) { queue.push(current.edges[i].n1); }
            }
        }
    }
    // ...............................................................
    ComputeDFS(start: number, val: Value) {
        this.DFS(this.nodes[start], val);
    }
    private DFS(n: Node, val: Value) {
        if (n.visited) { return val; }
        val.Add(n.id + 1);
        n.visited = true;
        // for (let i = 0; i < n.edges.length; ++i) {
        //    this.DFS(n.edges[i].n0, val);
        //    this.DFS(n.edges[i].n1, val);
        // }
        for (let i = n.edges.length - 1; i > -1; --i) {
            this.DFS(n.edges[i].n0, val);
            this.DFS(n.edges[i].n1, val);
        }
        return val;
    }
    // ...............................................................
    Render(ctx: CanvasRenderingContext2D, p: number[]) {
        for (let i = 0; i < this.edges.length; ++i) {
            this.edges[i].Render(ctx);
        }
        for (let i = 0; i < this.nodes.length; ++i) {
            this.nodes[i].Render(ctx);
        }
    }
}
class Edge {
    n0: Node;
    n1: Node;
    constructor(n0: Node, n1: Node) {
        this.n0 = n0;
        this.n1 = n1;
        this.n0.edges.push(this);
        this.n1.edges.push(this);
    }
    Render(ctx: CanvasRenderingContext2D) {
        ctx.beginPath();
        ctx.moveTo(this.n0.x, this.n0.y);
        ctx.lineTo(this.n1.x, this.n1.y);
        ctx.closePath();
        ctx.stroke();
    }
}
class Node {
    edges: Edge[] = [];
    x: number = -1;
    y: number = -1;
    id: number = -1;
    visited: boolean = false;
    constructor(id: number) {
        this.x = (Math.random() + 1) * 300;
        this.y = (Math.random() + 1) * 300;
        this.id = id;
        console.log(this.x, this.y);
    }
    Render(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#ff0000';
        ctx.beginPath();
        ctx.arc(this.x, this.y, 10, 0, 3.14 * 2);
        ctx.closePath();
        ctx.stroke();

        ctx.font = '15px Arial';
        ctx.fillText((this.id + 1).toString(), this.x - 5, this.y + 5);
    }
}