export class BoidContoller1 {

  boids: Boid[] = [];
  targets: NVector3[];

  constructor(targets: NVector3[]) {
    this.boids = [];
    this.targets = targets;

    // for (let j = 0; j < p0.length; ++j ) {
    //   for (let i = 0; i < 10; ++i ) {
    //     let boid = new Boid(p0[j]);
    //     boid.InitTarget(targets);
    //     this.boids.push(boid);
    //   }
    // }

  }
  public AddAgent(x: number, y: number) {
    let boid = new Boid(new NVector3(x, y));
    boid.InitTarget(this.targets);
    this.boids.push(boid);
  }
  public Update() {
    this.Col();
    for (let b of this.boids) {
      b.Update();
    }

  }
  public Col() {
    for (let b of this.boids) {
      b.col = false;
    }

    for (let j = 0; j < this.boids.length; ++j) {
      let b0 = this.boids[j];
      if (!b0.isRenderable) {
        break;
      }
      for (let i = j + 1; i < this.boids.length; ++i) {
        let b1 = this.boids[i];
        let dis = b0.p.Distance(b1.p);
        if (dis < 30) {
          b1.col = true;
          break;
        }
      }
    }

  }
  public GetPoint() {
    let pts: NVector3[] = [];
    for (let b of this.boids) {
      if (b.isRenderable) {
        pts.push(b.p);
      }
    }
    return pts;
  }

  // .......................................................................................................................

  Render(ctx: CanvasRenderingContext2D) {
    this.boids.forEach((b) => {
      b.Render(ctx);
    });
  }
}

export class Boid {

  static list: Boid[] = [];

  maxForce = 5; // 1; // 5;
  maxVelocity = 5; // 1; // 8;
  maxSpeed = 5; // 1; // 13.0;
  mass = 10.2;

  isRenderable = true;

  p: NVector3;
  targets: NVector3[];
  target: NVector3;
  targetID = 0;

  col: boolean = false;
  desiredVelocity: NVector3;
  steering: NVector3;
  velocity: NVector3 = new NVector3(0, 0, 0);

  constructor(p: NVector3) {
    this.p = p;
    this.target = new NVector3(p.x, p.y, p.z);
  }
  public InitTarget(targets: NVector3[]) {
    // Boid.list.Add(this);
    this.targets = targets;
    this.target = this.targets[this.targetID];
  }
  public Update() {
    this.UpdateTarget();
    // this.UpdateVec0();
    // this.UpdateVec1();
    // this.UpdateVecSlowDown1();
    this.UpdateVecSlowDown2();

  }
  UpdateTarget() {
    if (this.p.Distance(this.target) < 9) {
      this.targetID++;
      if (this.targetID < this.targets.length) {
        this.target = this.targets[this.targetID];
        if (this.targetID === this.targets.length - 1) {

          // if (exitBool) {
          //     this.isRenderable = false;
          // }
        }
      }
    }
  }
  UpdateVec0() {
    this.desiredVelocity = new NVector3(this.target.x - this.p.x, this.target.y - this.p.y, this.target.z - this.p.z);
    this.desiredVelocity.Unitize();
    this.desiredVelocity.x *= this.maxVelocity;
    this.desiredVelocity.y *= this.maxVelocity;
    this.desiredVelocity.z *= this.maxVelocity;

    let x = (this.desiredVelocity.x - this.velocity.x) < this.maxForce ? this.desiredVelocity.x - this.velocity.x : this.maxForce;
    let y = (this.desiredVelocity.y - this.velocity.y) < this.maxForce ? this.desiredVelocity.y - this.velocity.y : this.maxForce;
    let z = (this.desiredVelocity.z - this.velocity.z) < this.maxForce ? this.desiredVelocity.z - this.velocity.z : this.maxForce;

    this.steering = new NVector3(x / this.mass, y / this.mass, z / this.mass);

    this.velocity.x = (this.velocity.x + this.steering.x) < this.maxSpeed ? this.velocity.x + this.steering.x : this.maxSpeed;
    this.velocity.y = (this.velocity.y + this.steering.y) < this.maxSpeed ? this.velocity.y + this.steering.y : this.maxSpeed;
    this.velocity.z = (this.velocity.z + this.steering.z) < this.maxSpeed ? this.velocity.z + this.steering.z : this.maxSpeed;

    if (!this.col) {
      // this.velocity = truncate(velocity + steering, maxSpeed);
      this.p.x += this.velocity.x;
      this.p.y += this.velocity.y;
      this.p.z += this.velocity.z;
    } else {
      this.p.x += -this.steering.x * 0.8;
      this.p.y += -this.steering.y * 0.8;
      this.p.z += -this.steering.z * 0.8;

    }
  }
  UpdateVec1() {
    this.desiredVelocity = new NVector3(this.target.x - this.p.x, this.target.y - this.p.y, this.target.z - this.p.z);
    // this.desiredVelocity = new NVector3(this.p.x - this.target.x , this.p.y - this.target.y ,  this.p.x - this.target.z );
    this.desiredVelocity.Unitize();
    this.desiredVelocity.x *= this.maxVelocity;
    this.desiredVelocity.y *= this.maxVelocity;
    this.desiredVelocity.z *= this.maxVelocity;

    let x = (this.desiredVelocity.x - this.velocity.x); // < this.maxForce ? this.desiredVelocity.x - this.velocity.x : this.maxForce;
    let y = (this.desiredVelocity.y - this.velocity.y); //  < this.maxForce ? this.desiredVelocity.y - this.velocity.y : this.maxForce;
    let z = (this.desiredVelocity.z - this.velocity.z); // < this.maxForce ? this.desiredVelocity.z - this.velocity.z : this.maxForce;

    this.steering = new NVector3(x / this.mass, y / this.mass, z / this.mass);

    this.velocity.x = (this.velocity.x + this.steering.x) < this.maxSpeed ? this.velocity.x + this.steering.x : this.maxSpeed;
    this.velocity.y = (this.velocity.y + this.steering.y) < this.maxSpeed ? this.velocity.y + this.steering.y : this.maxSpeed;
    this.velocity.z = (this.velocity.z + this.steering.z) < this.maxSpeed ? this.velocity.z + this.steering.z : this.maxSpeed;

    if (!this.col) {
      // this.velocity = truncate(velocity + steering, maxSpeed);
      this.p.x += this.velocity.x;
      this.p.y += this.velocity.y;
      this.p.z += this.velocity.z;
    } else {
      this.p.x += -this.steering.x * 0.8;
      this.p.y += -this.steering.y * 0.8;
      this.p.z += -this.steering.z * 0.8;

    }
  }
  UpdateVecSlowDown1() {
    this.desiredVelocity = new NVector3(this.target.x - this.p.x, this.target.y - this.p.y, this.target.z - this.p.z);
    // this.desiredVelocity = new NVector3(this.p.x - this.target.x , this.p.y - this.target.y ,  this.p.x - this.target.z );
    this.desiredVelocity.Unitize();
    this.desiredVelocity.x *= this.maxVelocity;
    this.desiredVelocity.y *= this.maxVelocity;
    this.desiredVelocity.z *= this.maxVelocity;

    let x = (this.desiredVelocity.x - this.velocity.x); // < this.maxForce ? this.desiredVelocity.x - this.velocity.x : this.maxForce;
    let y = (this.desiredVelocity.y - this.velocity.y); //  < this.maxForce ? this.desiredVelocity.y - this.velocity.y : this.maxForce;
    let z = (this.desiredVelocity.z - this.velocity.z); // < this.maxForce ? this.desiredVelocity.z - this.velocity.z : this.maxForce;

    this.steering = new NVector3(x / this.mass, y / this.mass, z / this.mass);

    this.velocity.x = (this.velocity.x + this.steering.x) < this.maxSpeed ? this.velocity.x + this.steering.x : this.maxSpeed;
    this.velocity.y = (this.velocity.y + this.steering.y) < this.maxSpeed ? this.velocity.y + this.steering.y : this.maxSpeed;
    this.velocity.z = (this.velocity.z + this.steering.z) < this.maxSpeed ? this.velocity.z + this.steering.z : this.maxSpeed;

    if (!this.col) {

      let i = this.maxSpeed / this.velocity.Length();
      i = (i < 1.0) ? i : 1.0;
      // this.velocity = truncate(velocity + steering, maxSpeed);
      this.p.x += this.velocity.x * i;
      this.p.y += this.velocity.y * i;
      this.p.z += this.velocity.z * i;
    } else {
      this.p.x += -this.steering.x * 0.8;
      this.p.y += -this.steering.y * 0.8;
      this.p.z += -this.steering.z * 0.8;
    }
  }
  UpdateVecSlowDown2() {
    this.desiredVelocity = new NVector3(this.target.x - this.p.x, this.target.y - this.p.y, this.target.z - this.p.z);
    // this.desiredVelocity = new NVector3(this.p.x - this.target.x , this.p.y - this.target.y ,  this.p.x - this.target.z );

    let slowingRadius = 150;
    let distance = this.desiredVelocity.Length();
    // Check the distance to detect whether the character
    // is inside the slowing area
    if (distance < slowingRadius) {
      // Inside the slowing area
      // desired_velocity = normalize(desired_velocity) * max_velocity * (distance / slowingRadius)
      this.desiredVelocity.Unitize();
      this.desiredVelocity.x *= this.maxVelocity * (distance / slowingRadius);
      this.desiredVelocity.y *= this.maxVelocity * (distance / slowingRadius);
      this.desiredVelocity.z *= this.maxVelocity * (distance / slowingRadius);
    } else {
      // Outside the slowing area.
      // desired_velocity = normalize(desired_velocity) * max_velocity
      this.desiredVelocity.Unitize();
      this.desiredVelocity.x *= this.maxVelocity;
      this.desiredVelocity.y *= this.maxVelocity;
      this.desiredVelocity.z *= this.maxVelocity;
    }
    // Set the steering based on this
    // steering = desired_velocity - velocity

    let x = (this.desiredVelocity.x - this.velocity.x); // < this.maxForce ? this.desiredVelocity.x - this.velocity.x : this.maxForce;
    let y = (this.desiredVelocity.y - this.velocity.y); //  < this.maxForce ? this.desiredVelocity.y - this.velocity.y : this.maxForce;
    let z = (this.desiredVelocity.z - this.velocity.z); // < this.maxForce ? this.desiredVelocity.z - this.velocity.z : this.maxForce;

    // this.steering = new NVector3(x / this.mass, y / this.mass, z / this.mass);
    this.steering = new NVector3(x, y, z);

    this.velocity.x = (this.velocity.x + this.steering.x) < this.maxSpeed ? this.velocity.x + this.steering.x : this.maxSpeed;
    this.velocity.y = (this.velocity.y + this.steering.y) < this.maxSpeed ? this.velocity.y + this.steering.y : this.maxSpeed;
    this.velocity.z = (this.velocity.z + this.steering.z) < this.maxSpeed ? this.velocity.z + this.steering.z : this.maxSpeed;

    if (!this.col) {

      let i = this.maxSpeed / this.velocity.Length();
      i = (i < 1.0) ? i : 1.0;
      // this.velocity = truncate(velocity + steering, maxSpeed);
      this.p.x += this.velocity.x * i;
      this.p.y += this.velocity.y * i;
      this.p.z += this.velocity.z * i;
    } else {
      this.p.x += -this.steering.x * 0.8;
      this.p.y += -this.steering.y * 0.8;
      this.p.z += -this.steering.z * 0.8;

    }
  }
  // .......................................................................................................................
  Render(ctx: CanvasRenderingContext2D) {
    this.RenderDesiredVelocity(ctx);
    this.RenderCurrentVelocity(ctx);
    this.RenderCircle(ctx);
  }
  RenderDesiredVelocity(ctx: CanvasRenderingContext2D) {
    let scale = 20;
    ctx.lineWidth = 0.5;
    ctx.strokeStyle = '#00ff00';
    ctx.beginPath();
    ctx.moveTo(this.p.x, this.p.y);
    ctx.lineTo(this.p.x + this.desiredVelocity.x * scale, this.p.y + this.desiredVelocity.y * scale);
    ctx.closePath();
    ctx.stroke();
  }
  RenderCurrentVelocity(ctx: CanvasRenderingContext2D) {
    let scale = 20;
    ctx.lineWidth = 0.5;
    ctx.strokeStyle = '#ff0000';
    ctx.beginPath();
    ctx.moveTo(this.p.x, this.p.y);
    ctx.lineTo(this.p.x + this.velocity.x * scale, this.p.y + this.velocity.y * scale);
    ctx.closePath();
    ctx.stroke();
  }
  RenderCircle(ctx: CanvasRenderingContext2D) {
    ctx.strokeStyle = '#000000';
    ctx.lineWidth = 0.5;
    ctx.beginPath();
    ctx.moveTo(this.targets[0].x, this.targets[0].y);
    for (let i = 1; i < this.targets.length; ++i) {
      ctx.lineTo(this.targets[i].x, this.targets[i].y);
    }
    ctx.closePath();
    ctx.stroke();

    ctx.beginPath();
    ctx.arc(this.p.x, this.p.y, 10, 0, 3.14 * 2);
    ctx.closePath();
    // ctx.fill();
    ctx.stroke();
  }
}

