/*
    Function of diffuse
    - b : int
    - x : float[]
    - x0 : float[]
    - diff : float
    - dt : flaot
*/
export const diffuse = (b: number, x: Float32Array, x0: Float32Array, diff: number, dt: number, N: number, iter: number, ids: number[][]) => {
    let a = dt * diff * (N - 2) * (N - 2);
    lin_solve(b, x, x0, a, 1 + 6 * a, N, iter, ids);
  };

  /*
      Function of solving linear differential equation
      - b : int
      - x : float[]
      - x0 : float[]
      - a : float
      - c : float
  */
export const lin_solve = (b: number, x: Float32Array, x0: Float32Array, a: number, c: number, N: number, iter: number, ids: number[][]) => {
    let cRecip = 1.0 / c;
    for (let t = 0; t < iter; t++) {
      for (let j = 1; j < N - 1; j++) {
        for (let i = 1; i < N - 1; i++) {
          x[IX(i, j, N)] = (x0[IX(i, j, N)] + a * (x[IX(i + 1, j, N)] + x[IX(i - 1, j, N)] + x[IX(i, j + 1, N)] + x[IX(i, j - 1, N)])) * cRecip;
        }
      }
      set_bnd(b, x, N, ids);
    }
};
  // function to use 1D array and fake the extra two dimensions --> 3D
export const IX = (x: number, y: number, N: number) => {
    return x + y * N;
};
  /*
      Function of project : This operation runs through all the cells and fixes them up so everything is in equilibrium.
      - velocX : float[]
      - velocY : float[]
      = p : float[]
      - div : float[]
  */
export const project = (velocX: Float32Array, velocY: Float32Array, p: Float32Array, div: Float32Array, N: number, iter: number, ids: number[][]) => {
    for (let j = 1; j < N - 1; j++) {
      for (let i = 1; i < N - 1; i++) {
        div[IX(i, j, N)] = (-0.5 * (velocX[IX(i + 1, j, N)] - velocX[IX(i - 1, j, N)] + velocY[IX(i, j + 1, N)] - velocY[IX(i, j - 1, N)])) / N;
        p[IX(i, j, N)] = 0;
      }
    }

    set_bnd(0, div, N, ids);
    set_bnd(0, p, N, ids);
    lin_solve(0, p, div, 1, 6, N, iter, ids);

    for (let j = 1; j < N - 1; j++) {
      for (let i = 1; i < N - 1; i++) {
        velocX[IX(i, j, N)] -= 0.5 * (p[IX(i + 1, j, N)] - p[IX(i - 1, j, N)]) * N;
        velocY[IX(i, j, N)] -= 0.5 * (p[IX(i, j + 1, N)] - p[IX(i, j - 1, N)]) * N;
      }
    }

    set_bnd(1, velocX, N, ids);
    set_bnd(2, velocY, N, ids);
};

  /*
      Function of advect: responsible for actually moving things around
      - b : int
      - d : float[]
      - d0 : float[]
      - velocX : float[]
      - velocY : float[]
      - velocZ : float[]
      - dt : float[]
  */
export const advect = (b: number, d: Float32Array, d0: Float32Array, velocX: Float32Array, velocY: Float32Array, dt: number, N: number, ids: number[][]) => {
    let i0, i1, j0, j1;

    let dtx = dt * (N - 2);
    let dty = dt * (N - 2);

    let s0, s1, t0, t1;
    let tmp1, tmp2, x, y;

    let Nfloat = N - 2;
    let ifloat, jfloat;
    let i, j;
    // let temp3, k;

    for (j = 1, jfloat = 1; j < N - 1; j++, jfloat++) {
      for (i = 1, ifloat = 1; i < N - 1; i++, ifloat++) {
        tmp1 = dtx * velocX[IX(i, j, N)];
        tmp2 = dty * velocY[IX(i, j, N)];
        x = ifloat - tmp1;
        y = jfloat - tmp2;

        if (x < 0.5) x = 0.5;
        if (x > Nfloat + 0.5) x = Nfloat + 0.5;
        i0 = Math.floor(x);
        i1 = i0 + 1.0;
        if (y < 0.5) y = 0.5;
        if (y > Nfloat + 0.5) y = Nfloat + 0.5;
        j0 = Math.floor(y);
        j1 = j0 + 1.0;

        s1 = x - i0;
        s0 = 1.0 - s1;
        t1 = y - j0;
        t0 = 1.0 - t1;

        let i0i = i0; // parseInt(i0);
        let i1i = i1; // parseInt(i1);
        let j0i = j0; // parseInt(j0);
        let j1i = j1; // parseInt(j1);

        d[IX(i, j, N)] = s0 * (t0 * d0[IX(i0i, j0i, N)] + t1 * d0[IX(i0i, j1i, N)]) + s1 * (t0 * d0[IX(i1i, j0i, N)] + t1 * d0[IX(i1i, j1i, N)]);
      }
    }

    set_bnd(b, d, N, ids);
};
  /*
      Function of dealing with situation with boundary cells.
      - b : int
      - x : float[]
  */
export const set_bnd = (b: number, x: Float32Array, N: number, ids: number[][]) => {
    for (let i = 1; i < N - 1; i++) {
      x[IX(i, 0, N)] = b === 2 ? -x[IX(i, 1, N)] : x[IX(i, 1, N)];
      x[IX(i, N - 1, N)] = b === 2 ? -x[IX(i, N - 2, N)] : x[IX(i, N - 2, N)];
    }
    for (let j = 1; j < N - 1; j++) {
      x[IX(0, j, N)] = b === 1 ? -x[IX(1, j, N)] : x[IX(1, j, N)];
      x[IX(N - 1, j, N)] = b === 1 ? -x[IX(N - 2, j, N)] : x[IX(N - 2, j, N)];
    }

    x[IX(0, 0, N)] = 0.5 * (x[IX(1, 0, N)] + x[IX(0, 1, N)]);
    x[IX(0, N - 1, N)] = 0.5 * (x[IX(1, N - 1, N)] + x[IX(0, N - 2, N)]);
    x[IX(N - 1, 0, N)] = 0.5 * (x[IX(N - 2, 0, N)] + x[IX(N - 1, 1, N)]);
    x[IX(N - 1, N - 1, N)] = 0.5 * (x[IX(N - 2, N - 1, N)] + x[IX(N - 1, N - 2, N)]);

    x[IX(Math.floor(N * 0.5), 0, N)] = 0;
    x[IX(Math.floor(N * 0.5), 1, N)] = 0;
    x[IX(Math.floor(N * 0.5), 2, N)] = 0;
    x[IX(Math.floor(N * 0.5), 3, N)] = 0;
    x[IX(Math.floor(N * 0.5), 4, N)] = 0;
    x[IX(Math.floor(N * 0.5), 5, N)] = 0;
    x[IX(Math.floor(N * 0.5), 6, N)] = 0;
    x[IX(Math.floor(N * 0.5), 7, N)] = 0;

    x[IX(Math.floor(N * 0.5), N - 1, N)] = 0;
    x[IX(Math.floor(N * 0.5), N - 2, N)] = 0;
    x[IX(Math.floor(N * 0.5), N - 3, N)] = 0;
    x[IX(Math.floor(N * 0.5), N - 4, N)] = 0;
    x[IX(Math.floor(N * 0.5), N - 5, N)] = 0;
    x[IX(Math.floor(N * 0.5), N - 6, N)] = 0;
    x[IX(Math.floor(N * 0.5), N - 7, N)] = 0;
    x[IX(Math.floor(N * 0.5), N - 8, N)] = 0;

    for (let i = 0 ; i < ids.length; ++i) {
      x[IX(ids[i][0], ids[i][1], N)] = 0;
    }
};