import { Canvas } from '../../Model/GraphicsCoreWrapper/CanvasWrapper/Canvas/Canvas';
import { ActiveCommandController } from './Core/ActiveCommandController';
import { GeomeetryCommon } from './Core/GeometryCommon';
import { DefinitionIndeCanvasCore } from './Core/DefinitionIndeCanvasCore';
import { BallBounce } from './Definition/BallBounce';
import { BallBounceClass } from './Definition/BallBounceClass';
// import { GaussianDistribution } from '../SceneVis/Definition/GaussianDistribution';
import { CircleGrid } from './Definition/CircleGrid';
import { CircleGridAttractors1 } from './Definition/CircleGridAttractors1';
import { CircleGridAttractors2 } from './Definition/CircleGridAttractors2';

import { DefinitionLine } from './Definition/DefinitionLine';
import { DefinitionLine2 } from './Definition/DefinitionLine2';

import { BoidSystem01 } from './Definition/BoidSystem/BoidSystem01';
import { BoidSystemWander0 } from './Definition/BoidSystem/BoidSystemWander0';
import { BoidSystemWander1 } from './Definition/BoidSystem/BoidSystemWander1';
import { BoidSystem04 } from './Definition/BoidSystem/BoidSystem04';

import { Dynamic01 } from './Definition/Dynamics/Dynamic01';
import { Dynamic02 } from './Definition/Dynamics/Dynamic02';
import { Dynamic03 } from './Definition/Dynamics/Dynamic03';

import { MatterSandbox } from './Definition/MatterSandbox';

import { OOP } from './Definition/OOP';

import { FluidSimulation } from './Definition/FluidSimulation';
import { PerlinHeatmap } from './Definition/PerlinHeatmap';
import { ColorPickerApp } from './Definition/ColorPicker';

import { AlgorithmMazeExit } from './Definition/AlgorithmMazeExit';
import { AlgorithmCabbage } from './Definition/AlgorithmCabbage';
import { AlgorithmBaconsLaw } from './Definition/AlgorithmBaconsLaw';
import { AlgorithmFireExit } from './Definition/AlgorithmFireExit';
import { AlgorithmNumOfArea } from './Definition/AlgorithmNumOfArea';
import { AlgorithmComputerFinder } from './Definition/AlgorithmComputerFinder';
import { AlgorithmKoze } from './Definition/AlgorithmKoze';
import { AlgorithmUnitMoving } from './Definition/AlgorithmUnitMoving';
import { AlgorithmTile } from './Definition/AlgorithmTile';
import { AlgorithmOKVIRI } from './Definition/AlgorithmOKVIRI';
import { AlgorithmDFSBFS } from './Definition/AlgorithmDFSBFS';

import { AlgorithmBuilding } from './Definition/AlgorithmBuilding';

import { GridOptiA } from './Definition/GridOpti/GridOptiA';

import { CustomDynamicBin } from './Definition/CustomDynamicBin';

import { Blank } from './Definition/Blank';
import { Implementation } from '..';
export class SceneCreativeCoding {
    implementation: Implementation;
    canvas: Canvas;
    activeCommandController: ActiveCommandController;
    geomeetryCommon: GeomeetryCommon;

    definitions: DefinitionIndeCanvasCore[] = [];

    constructor(implementation: Implementation) {
        this.implementation = implementation;
        this.canvas = implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas;

        this.activeCommandController = new ActiveCommandController(this);
        this.geomeetryCommon = new GeomeetryCommon(this);
        this.Init();
        this.InitDefinition();
    }
    public Init() {
        this.definitions = [];
    }
    get width() {
        return this.implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas.canvas.width;
    }
    get height() {
        return this.implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas.canvas.height;
    }
    InitDefinition() {
        for (let d of this.definitions) { d.init(this.implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas.ctx); }
    }
    Render(ctx: CanvasRenderingContext2D) {
        // for (let d of this.definitions) {d.Render(ctx); }
    }
    MouseMove(x: number, y: number) {
        for (let d of this.definitions) { d.MouseMovePre(x, y); }
    }
    MouseLeftClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseLeftClickPre(x, y); }
    }
    MouseDown(x: number, y: number) {
        for (let d of this.definitions) { d.MouseDownPre(x, y); }
    }
    MouseUp(x: number, y: number) {
        for (let d of this.definitions) { d.MouseUpPre(x, y); }
    }
    MouseDrag(x: number, y: number) {
        for (let d of this.definitions) { d.MouseDragPre(x, y); }
    }
    MouseMiddleClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseMiddleClickPre(x, y); }
    }
    MouseRightClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseRightClickPre(x, y); }
    }
    MouseDoubleClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseDoubleClickPre(x, y); }
    }
    MouseWheel(x: number, y: number, v: number) {
        for (let d of this.definitions) { d.MouseWheelPre(x, y, v); }
    }
    Dispose() {
        this.definitions = [];
    }
    LoadDefinition(v: string) {
        // this.definitions = [];
        if (v === 'Blank') { const d0 = new Blank(this); d0.Dummy(); }
        // if (v === 'GaussianDistribution') { const d0 = new GaussianDistribution(this); d0.Dummy(); }
        if (v === 'BallBounce') { const d0 = new BallBounce(this); d0.Dummy(); }
        if (v === 'BallBounceClass') { const d0 = new BallBounceClass(this); d0.Dummy(); }
        if (v === 'CircleGrid') { const d0 = new CircleGrid(this); d0.Dummy(); }
        // if (v === 'CirclePopulate') { const d0 = new CirclePopulate(this); d0.Dummy(); }
        if (v === 'CircleGridAttractors1') { const d0 = new CircleGridAttractors1(this); d0.Dummy(); }
        if (v === 'CircleGridAttractors2') { const d0 = new CircleGridAttractors2(this); d0.Dummy(); }
        if (v === 'Line') { const d0 = new DefinitionLine(this); d0.Dummy(); }
        if (v === 'Line2') { const d0 = new DefinitionLine2(this); d0.Dummy(); }

        if (v === 'BoidSystem01') { const d0 = new BoidSystem01(this); d0.Dummy(); }
        if (v === 'BoidSystem02') { const d0 = new BoidSystemWander0(this); d0.Dummy(); }
        if (v === 'BoidSystem03') { const d0 = new BoidSystemWander1(this); d0.Dummy(); }
        if (v === 'BoidSystem04') { const d0 = new BoidSystem04(this); d0.Dummy(); }

        if (v === 'DynamicSystem01') { const d0 = new Dynamic01(this); d0.Dummy(); }
        if (v === 'DynamicSystem02') { const d0 = new Dynamic02(this); d0.Dummy(); }
        if (v === 'DynamicSystem03') { const d0 = new Dynamic03(this); d0.Dummy(); }

        if (v === 'MatterSandbox') { const d0 = new MatterSandbox(this); d0.Dummy(); }

        if (v === 'OOP01') { const d0 = new OOP(this); d0.Dummy(); }

        if (v === 'FireExit') { const d0 = new AlgorithmFireExit(this); d0.Dummy(); }
        if (v === 'BaconsLaw') { const d0 = new AlgorithmBaconsLaw(this); d0.Dummy(); }
        if (v === 'Cabbage') { const d0 = new AlgorithmCabbage(this); d0.Dummy(); }
        if (v === 'NumOfArea') { const d0 = new AlgorithmNumOfArea(this); d0.Dummy(); }
        if (v === 'ComputerFinder') { const d0 = new AlgorithmComputerFinder(this); d0.Dummy(); }
        if (v === 'MazeExit') { const d0 = new AlgorithmMazeExit(this); d0.Dummy(); }

        if (v === 'Koze') { const d0 = new AlgorithmKoze(this); d0.Dummy(); }
        if (v === 'UnitMoving') { const d0 = new AlgorithmUnitMoving(this); d0.Dummy(); }
        if (v === 'Tile') { const d0 = new AlgorithmTile(this); d0.Dummy(); }
        if (v === 'OKVIRI') { const d0 = new AlgorithmOKVIRI(this); d0.Dummy(); }

        if (v === 'AlgorithmDFSBFS') { const d0 = new AlgorithmDFSBFS(this); d0.Dummy(); }

        if (v === 'GridOptiA') { const d0 = new GridOptiA(this); d0.Dummy(); }
        if (v === 'FluidSimulation') { const d0 = new FluidSimulation(this); d0.Dummy(); }
        if (v === 'PerlinHeatmap') { const d0 = new PerlinHeatmap(this); d0.Dummy(); }
        if (v === 'ColorPicker') { const d0 = new ColorPickerApp(this); d0.Dummy(); }

        if (v === 'temp') { const d0 = new AlgorithmMazeExit(this); d0.Dummy(); }
        if (v === 'temp') { const d0 = new AlgorithmMazeExit(this); d0.Dummy(); }
        if (v === 'temp') { const d0 = new AlgorithmMazeExit(this); d0.Dummy(); }
        if (v === 'temp') { const d0 = new AlgorithmMazeExit(this); d0.Dummy(); }

        if (v === 'Building') { const d0 = new AlgorithmBuilding(this); d0.Dummy(); }

        if (v === 'customBinSystemA') { const d0 = new CustomDynamicBin(this); d0.Dummy(); }
    }
}