import { diffuse, project, advect, IX } from './Utility';

import { getFalseColor } from '../../../../../../../../NJSCore/Core/NColor/Utility/ColorUtility';
// Fluid cube class
export class Fluid {

  public N = 100;
  public iter = 16;
  public SCALE = 4;
  public t = 0;

  public visScale = 0.01;

  // size: number;
  public dt: number;
  public diff: number;
  public visc: number;

  public s: Float32Array;
  public density: Float32Array;
  public Vx: Float32Array;
  public Vy: Float32Array;
  public Vx0: Float32Array;
  public Vy0: Float32Array;

  public cellFixed: number[][] = [];

  constructor(dt: number, diffusion: number, viscosity: number) {
    // this.size = this.SCALE;
    this.dt = dt;
    this.diff = diffusion;
    this.visc = viscosity;

    this.s = new Float32Array(this.N * this.N).fill(0);
    this.density = new Float32Array(this.N * this.N).fill(0.01);

    this.Vx = new Float32Array(this.N * this.N).fill(0);
    this.Vx0 = new Float32Array(this.N * this.N).fill(0);

    this.Vy = new Float32Array(this.N * this.N).fill(0);
    this.Vy0 = new Float32Array(this.N * this.N).fill(0);
  }
  // step method
  public step() {
    // let N = this.size;
    let visc = this.visc;
    let diff = this.diff;
    let dt = this.dt;
    let Vx = this.Vx;
    let Vy = this.Vy;
    let Vx0 = this.Vx0;
    let Vy0 = this.Vy0;
    let s = this.s;
    let density = this.density;

    diffuse(1, Vx0, Vx, visc, dt, this.N, this.iter, this.cellFixed);
    diffuse(2, Vy0, Vy, visc, dt, this.N, this.iter, this.cellFixed);

    project(Vx0, Vy0, Vx, Vy, this.N, this.iter, this.cellFixed);

    advect(1, Vx, Vx0, Vx0, Vy0, dt, this.N, this.cellFixed);
    advect(2, Vy, Vy0, Vx0, Vy0, dt, this.N, this.cellFixed);

    project(Vx, Vy, Vx0, Vy0, this.N, this.iter, this.cellFixed);
    diffuse(0, s, density, diff, dt, this.N, this.iter, this.cellFixed);
    advect(0, density, s, Vx, Vy, dt, this.N, this.cellFixed);

  }

  // method to add density
  public addDensity(x: number, y: number, amount: number) {
    let index = IX(x, y, this.N);
    this.density[index] += amount;
  }
  // method to add velocity
  public addVelocity(x: number, y: number, amountX: number, amountY: number) {
    let index = IX(x, y, this.N);
    this.Vx[index] += amountX;
    this.Vy[index] += amountY;
  }
  // function to render density
  public renderD(ctx: CanvasRenderingContext2D) {
    for (let i = 0; i < this.N; i++) {
      for (let j = 0; j < this.N; j++) {
        let x = i * this.SCALE;
        let y = j * this.SCALE;
        let d = this.density[IX(i, j, this.N)];

        // let col = `rgb(${(d + 50) % 255}, 200,${d})`;
        // console.log('ddd', `${(d + 50) % 255}, 200,${d}`);
        // console.log(this.density);
        // fill(d);
        // noStroke();
        // rect(x, y, this.SCALE, this.SCALE);
        // console.log(d);
        if (d === 0) {
          ctx.fillStyle = `rgb(255, 255, 255)`;
        } else {
          const col = getFalseColor(d * this.visScale);
          ctx.fillStyle = `rgb(${col[0]}, ${col[1]},${col[2]})`;
        }
        ctx.beginPath();
        ctx.rect(x, y, this.SCALE, this.SCALE);
        ctx.fill();
        ctx.lineWidth = 0.2;
        ctx.strokeStyle = '#ff0000';
        // ctx.stroke()
      }
    }
  }

  // function to render velocity
  public renderV(ctx: CanvasRenderingContext2D) {
    for (let i = 0; i < this.N; i++) {
      for (let j = 0; j < this.N; j++) {
        let x = i * this.SCALE;
        let y = j * this.SCALE;
        // let vx = this.Vx[IX(i, j, k)];
        // let vy = this.Vy[IX(i, j, k)];
        let vx = this.Vx[IX(i, j, this.N)];
        let vy = this.Vy[IX(i, j, this.N)];
        // this.canvas.stroke(0);

        if (!(Math.abs(vx) < 0.1 && Math.abs(vy) <= 0.1)) {
          ctx.lineWidth = 2;
          ctx.beginPath();
          ctx.moveTo(x, y);
          ctx.lineTo(x + vx * this.SCALE, y + vy * this.SCALE);
          ctx.stroke();
        //   line(x, y, x + vx * this.SCALE, y + vy * this.SCALE);
        }
      }
    }
  }

}