import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class AlgorithmTile extends DefinitionIndeCanvasCore {
    p: number[] = [0, 0];
    map: PixelMap;
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.init();

    }
    init() {
        const num = 11;
        const array = [
            [5, 2],
            [9, 7],
            [4, 4],
            [3, 9]
        ];

        let before = performance.now();
        this.map = new PixelMap(num, array);
        let after = performance.now();
        console.log('time: ', after - before);
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.map.Render(ctx, this.p);
        ctx.fillStyle = '#ff0000';
        ctx.beginPath();
        ctx.arc(this.p[0], this.p[1], 5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();

    }
    MouseMove(x: number, y: number) {
        this.p = [x, y];
    }
    MouseDown(x: number, y: number) {
        this.p = [x, y];
    }
}

// https://www.acmicpc.net/problem/5556

class Pixel {
    map: PixelMap;
    x: number = -1;
    y: number = -1;
    connections: Pixel[] = [];
    scale = 10;
    r: boolean = false;
    yC: boolean = false;
    b: boolean = false;
    selected: boolean = false;
    constructor(map: PixelMap, x: number, y: number, scale: number) {
        this.map = map;
        this.x = x;
        this.y = y;
        this.scale = scale;
    }
    Render(ctx: CanvasRenderingContext2D, p: number[]) {
        ctx.strokeStyle = '#ffffff';
        ctx.fillStyle = 'rgba(255,255,255, 1)';
        ctx.lineWidth = 1;
        ctx.beginPath();
        ctx.rect(this.x, this.y, this.scale, this.scale);
        ctx.closePath();
        if (this.r) {
            ctx.fillStyle = 'rgba(255,0,0, 1)';
        } else if (this.yC) {
            ctx.fillStyle = 'rgba(255,255,0, 1)';
        } else if (this.b) {
            ctx.fillStyle = 'rgba(0,0,255, 1)';
        }
        ctx.stroke();
        ctx.fill();

        if (this.selected) {
            ctx.fillStyle = '#ffffff';
            ctx.beginPath();
            ctx.arc(this.x + this.scale * 0.5, this.y + this.scale * 0.5, 5, 0, 3.14 * 2);
            ctx.closePath();
            ctx.fill();
        }
    }
}
class PixelMap {
    scale: number = 15;
    pixels: Pixel[][] = [];
    pixelsComputedNeeded: Pixel[] = [];
    s: number = 0;
    w: number = 0;
    constructor(n: number, data: number[][]) {
        for (let y = 0; y < n; ++y) {
            const pixelsRow = [];
            for (let x = 0; x < n; ++x) {
                pixelsRow.push(new Pixel(this, y * this.scale, x * this.scale, this.scale));
            }
            this.pixels.push(pixelsRow);
        }
        this.InitConnections();
        this.ComputeData(data);
    }
    ComputeData(data: number[][]) {
        for (let i = 0; i < data.length; ++i) {
            const p = this.pixels[data[i][0] - 1][data[i][1] - 1];
            p.selected = true;
            if (p.r) {
                console.log('red, 1');
            } else if (p.yC) {
                console.log('yellow, 3');
            } else if (p.b) {
                console.log('blue, 2');
            }
        }
    }
    InitConnections() {
        // right up
        for (let y = 0; y < this.pixels.length * 0.5; ++y) {
            for (let x = 0; x < this.pixels[0].length * 0.5; ++x) {
                const theCell = this.pixels[y][x];

                let xxx = x % 3;
                let yyy = y % 3;

                if (xxx === 0 && y >= x) {
                    theCell.r = true;
                } else if (xxx === 1 && y >= x) {
                    theCell.b = true;
                } else if (xxx === 2 && y >= x) {
                    theCell.yC = true;
                }

                if (yyy === 0 && x >= y) {
                    theCell.r = true;
                } else if (yyy === 1 && x >= y) {
                    theCell.b = true;
                } else if (yyy === 2 && x >= y) {
                    theCell.yC = true;
                }
            }
        }

        // left bottom
        for (let y = 0; y < this.pixels.length * 0.5; ++y) {
            for (let x = 0; x < this.pixels[0].length * 0.5; ++x) {
                const theCell = this.pixels[y][x];
                let yi = y; // this.pixels.length - y - 1;
                let xi = this.pixels[0].length - x - 1;
                const otherCell = this.pixels[yi][xi];
                otherCell.r = theCell.r;
                otherCell.yC = theCell.yC;
                otherCell.b = theCell.b;

            }
        }

        // right
        for (let y = 0; y < this.pixels.length * 0.5; ++y) {
            for (let x = 0; x < this.pixels[0].length; ++x) {
                const theCell = this.pixels[y][x];
                let yi = this.pixels.length - y - 1;
                let xi = x; // this.pixels[0].length - x - 1;
                const otherCell = this.pixels[yi][xi];
                otherCell.r = theCell.r;
                otherCell.yC = theCell.yC;
                otherCell.b = theCell.b;

            }
        }
    }
    Render(ctx: CanvasRenderingContext2D, p: number[]) {
        for (let y = 0; y < this.pixels.length; ++y) {
            for (let x = 0; x < this.pixels[0].length; ++x) {
                this.pixels[y][x].Render(ctx, p);
            }
        }
    }
}