import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class AlgorithmBaconsLaw extends DefinitionIndeCanvasCore {
    p: number[] = [0, 0];
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        const exe = new Exe();
        console.log(exe);
    }
    init() {
        //
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#ff0000';
        ctx.beginPath();
        ctx.arc(this.p[0], this.p[1], 5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();
    }
    MouseDown(x: number, y: number) {
        this.p = [x, y];
    }
}

/* data
5 5
1 3
1 4
4 5
4 3
3 2
*/

class Person {
    id: number = -1;
    friends: Person[] = [];
    dis: number = 0;
    visited: boolean = false;
    constructor(id: number) {
        this.id = id;
    }
    push(friend: Person) {
        for (let i = 0; i < this.friends.length; ++i) {
            if (this.friends[i] === friend) { return; }
        }
        this.friends.push(friend);
        friend.push(this);
    }
    Reset() {
        this.visited = false;
        this.dis = 0;
    }
    findPerson(id: number): number {
        // let count = 1;
        let list = [];
        if (this.friends.length === 0) { return -1; }

        this.visited = true;
        list.push(this);

        while (list.length > 0) {
            let person: Person = list[0];
            list.shift();

            if (person) {
                if (id === person.id) { return person.dis; }

                for (let i = 0; i < person.friends.length; ++i) {
                    if (person.friends[i] && !person.friends[i].visited) {
                        person.friends[i].visited = true;
                        person.friends[i].dis = person.dis + 1;
                        list.push(person.friends[i]);
                    }
                }
            }
        }
        return -1;
    }
}

class Exe {
    people: Person[] = [];
    index: number = 0;
    constructor() {
        const numPeople = 5;
        for (this.index = 0; this.index < numPeople; ++this.index) { this.people.push(new Person(this.index)); }
        const data = [
            [1, 3],
            [1, 4],
            [4, 5],
            [4, 3],
            [3, 2]
        ];
        for (this.index = 0; this.index < data.length; ++this.index) {
            this.BuildRelationship(data[this.index][0] - 1, data[this.index][1] - 1);
        }
        this.PrintRelationship();
        this.Compute(numPeople);
    }
    Reset() {
        for (this.index = 0; this.index < this.people.length; ++this.index) {

            this.people[this.index].visited = false;
            this.people[this.index].dis = 0;
        }
    }
    BuildRelationship(a: number, b: number) {
        this.people[a].push(this.people[b]);
    }
    PrintRelationship() {
        for (this.index = 0; this.index < this.people.length; ++this.index) {
            let frendsindex = '';
            for (let j = 0; j < this.people[this.index].friends.length; ++j) {
                frendsindex += ` ${this.people[this.index].friends[j].id}`;
            }

            console.log('id: ', this.people[this.index].id, ', freindsID', frendsindex);
        }
    }
    Compute(numPeople: number) {
        for (let i = 0; i < numPeople; ++i) {
            let sum = 0;
            for (let j = 0; j < numPeople; ++j) {
                if (i !== j) {
                    let count = this.people[i].findPerson(j);
                    sum += count;
                    console.log(i, j, count);
                    this.Reset();
                }
            }
            console.log(i, sum);
            console.log('------');
            // break;
        }
    }
}