// https://www.acmicpc.net/problem/5427

class Pixel {
    map: PixelMap;
    hover: boolean = false;
    x: number = -1;
    y: number = -1;
    type: string;
    scale: number = 50;
    fill: boolean = false;
    fire: boolean = false;
    exit: boolean = false;
    visited: boolean = false;
    cost: number = 0;
    temp: number = 0;
    connections: Pixel[] = [];
    constructor(map: PixelMap, x: number, y: number) {
        this.map = map;
        this.x = x;
        this.y = y;
    }
    Render(ctx: CanvasRenderingContext2D) {
        // let xPos = (this.x + .5);
        // let yPos = (this.y + .5);

        // const dis = Math.sqrt( (p[0] - xPos) * (p[0] - xPos) + (p[1] - yPos) * (p[1] - yPos));
        ctx.lineWidth = 4;
        ctx.strokeStyle = '#ffffff';
        ctx.beginPath();
        ctx.rect(50 + this.x - (this.scale * 0.5), 50 + this.y - (this.scale * 0.5), this.scale, this.scale);
        ctx.closePath();
        if (this.fill) {
            ctx.fillStyle = 'rgba(0,0,0, 0.85)';
        } else if (this.hover) {
            ctx.fillStyle = 'rgba(255,0,0, 0.8)';
        } else {
            ctx.fillStyle = 'rgba(255,255,0, 0.5)';
        }
        ctx.fill();
        ctx.stroke();

        ctx.fillStyle = '#0000ff';
        ctx.beginPath();
        ctx.arc(50 + this.x, 50 + this.y, 3, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();

        // if (dis < 0.5 * this.scale) {
        //     ctx.lineWidth = 1;
        //     for (let i = 0 ; i < this.connections.length; ++i) {
        //         ctx.beginPath();
        //         ctx.moveTo((this.x + .5) * this.scale, (this.y + .5) * this.scale);
        //         ctx.lineTo((this.connections[i].x + .5) * this.scale, (this.connections[i].y + .5) * this.scale);
        //         // ctx.closePath();
        //         ctx.stroke();
        //     }
        // }
        // if (this.exit) {
        //     ctx.fillStyle = '#0000ff';
        //     ctx.beginPath();
        //     ctx.arc((this.x + .5) * this.scale, (this.y + .5) * this.scale, 5, 0, 3.14 * 2);
        //     ctx.closePath();
        //     ctx.fill();
        // }
    }
    RenderHover(ctx: CanvasRenderingContext2D) {
        this.hover = true;
        this.Render(ctx);
    }
}

class GhostPixel {
    map: PixelMap;
    pixels: Pixel[] = [];
    connections: GhostPixel[] = [];
    x: number;
    y: number;
    isPath: boolean = true;
    visited: boolean = false;
    scale = 50;
    start: boolean = false;
    end: boolean = false;
    temp: number = -1;
    constructor(map: PixelMap, x: number, y: number) {
        this.x = x;
        this.y = y;
        this.map = map;
    }
    Compute() {
        for (let i = 0; i < this.pixels.length; ++i) {
            if (this.pixels[i].fill) {
                this.isPath = false;
                return;
            }
        }
    }
    pushConnections(p: GhostPixel) {
        if (!p.isPath) { return; }
        this.connections.push(p);
    }
    Render(ctx: CanvasRenderingContext2D, p: number[]) {
        const dis = Math.sqrt((p[0] - this.x) * (p[0] - this.x) + (p[1] - this.y) * (p[1] - this.y));

        if (this.isPath) {
            ctx.fillStyle = 'rgba(0,0,0, 1)';
        } else {
            ctx.fillStyle = 'rgba(0,0,0, 0.3)';
        }
        for (let i = 0; i < this.pixels.length; ++i) {
            if (dis < 10 && this.isPath) {
                this.map.pixelsHover = this.pixels;
            } else {
                this.pixels[i].hover = false;
            }
        }
        ctx.beginPath();
        ctx.arc(this.x, this.y, 9, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();

        // console.log(dis);
        if (dis < 10) {
            ctx.strokeStyle = '#000000';
            ctx.lineWidth = 10;
            for (let i = 0; i < this.connections.length; ++i) {
                ctx.beginPath();
                ctx.moveTo((this.x), (this.y));
                ctx.lineTo((this.connections[i].x), (this.connections[i].y));
                // ctx.closePath();
                ctx.stroke();
            }
        }

        if (this.start) {
            ctx.fillStyle = 'rgba(255,0,255 , 1)';
            ctx.beginPath();
            ctx.arc(this.x, this.y, 12, 0, 3.14 * 2);
            ctx.closePath();
            ctx.fill();
        }
        if (this.end) {
            ctx.fillStyle = 'rgba(0,255,255, 1)';
            ctx.beginPath();
            ctx.arc(this.x, this.y, 12, 0, 3.14 * 2);
            ctx.closePath();
            ctx.fill();
        }
    }
}
class PixelMap {
    connectionMap: GhostPixel[][] = [];
    pixels: Pixel[][] = [];
    pixelsHover: Pixel[] = [];
    scale = 50;
    fires: Pixel[] = [];
    exit: Pixel;
    path: GhostPixel[] | undefined = [];
    t = 0.0;
    count = 1;
    fail: boolean = false;
    done: boolean = false;
    constructor(data: number[], array: number[][]) {
        for (let i = 0; i < data[0]; ++i) {
            const pixelsRow = [];
            for (let j = 0; j < data[1]; ++j) {
                pixelsRow.push(new Pixel(this, j * this.scale, i * this.scale));
            }
            this.pixels.push(pixelsRow);
        }
        for (let i = 0; i < array.length - 2; ++i) {
            this.pixels[array[i][1]][array[i][0]].fill = true;
        }
        this.InitConnections(data[2], data[3]);
        // this.ComputeExit();
        // console.log(this);
        const start = this.FindGhost(array[array.length - 2]);
        if (start) {
            start.start = true;
        }
        const end = this.FindGhost(array[array.length - 1]);
        if (end) {
            end.end = true;
        }
        if (start && end) {
            this.path = this.ComputePath(start, end);
        }
        // console.log(this.path);
    }
    FindGhost(d: number[]) {
        const p = this.pixels[d[0]][d[1]];
        let gPixel: GhostPixel | undefined;
        for (let y = 0; y < this.connectionMap.length; ++y) {
            for (let x = 0; x < this.connectionMap[0].length; ++x) {
                for (let i = 0; i < this.connectionMap[y][x].pixels.length; ++i) {
                    if (this.connectionMap[y][x].pixels[i] === p && this.connectionMap[y][x].isPath) {
                        gPixel = this.connectionMap[y][x];
                    }
                }
            }
        }
        if (gPixel) {
            return gPixel;
        }
        return undefined;
    }
    Update() {
        // this.ComputeFire();
        // this.ComputeAgent();
    }
    ComputeFire() {
        let addition = [];
        for (let i = 0; i < this.fires.length; ++i) {
            for (let j = 0; j < this.fires[i].connections.length; ++j) {
                if (!this.fires[i].connections[j].fill) {
                    this.fires[i].connections[j].fire = true;
                    addition.push(this.fires[i].connections[j]);
                }
            }
        }
        for (let i = 0; i < addition.length; ++i) {
            this.fires.push(addition[i]);
        }
    }
    ComputeExit() {
        for (let y = 0; y < this.pixels.length; ++y) {
            for (let x = 0; x < this.pixels[y].length; ++x) {
                if (this.pixels[y][x].connections.length < 3 && y === 0 || x === 0 || y === this.pixels.length - 1 || x === this.pixels[0].length - 1) {
                    if (!this.pixels[y][x].fill) {
                        let hasCon = false;
                        for (let i = 0; i < this.pixels[y][x].connections.length; ++i) {
                            if (this.pixels[y][x].connections[i].fill === false) {
                                hasCon = true;
                            }
                        }
                        if (hasCon) {
                            this.pixels[y][x].exit = true;
                            this.exit = this.pixels[y][x];
                        }
                    }
                }
            }
        }
    }
    ResetCost() {
        for (let y = 0; y < this.connectionMap.length; ++y) {
            for (let x = 0; x < this.connectionMap[y].length; ++x) {
                if (this.connectionMap[y][x].isPath) {
                    this.connectionMap[y][x].temp = -1.0;
                    this.connectionMap[y][x].visited = false;
                }
            }
        }
    }
    ComputePath(startP: GhostPixel, endP: GhostPixel): GhostPixel[] | undefined {
        this.ResetCost();

        let queueForward = [startP];
        while (queueForward.length > 0) {
            let current = queueForward[0];
            current.visited = true;
            queueForward.shift();
            for (let i = 0; i < current.connections.length; ++i) {
                if (!current.connections[i].visited) {
                    const newcost = this.ComputeCost(current, current.connections[i]);
                    const newtemp = current.temp + newcost;
                    if (current.connections[i].temp === -1.0 || newtemp < current.connections[i].temp) {
                        current.connections[i].temp = newtemp;
                        queueForward.push(current.connections[i]);
                    }
                }
            }
        }

        let path: GhostPixel[] = [];
        if (endP.temp !== -1.0) {
            path.push(endP);
            let current = endP;
            while (true) {
                let smallest = current;
                for (let i = 0; i < current.connections.length; ++i) {
                    if (current.connections[i].isPath) {
                        if (current.connections[i].temp < smallest.temp) { smallest = current.connections[i]; }
                    }
                }
                if (smallest === current) { this.fail = true; return null; }
                current = smallest;
                path.push(current);
                if (current === startP) { break; }
            }
        }
        return path;
        // console.log('there are ', count, ' group of cells' );
    }
    ComputeCost(current: GhostPixel, target: GhostPixel): number {
        return Math.sqrt((target.x - current.x) * (target.x - current.x) + (target.y - current.y) * (target.y - current.y));
    }
    InitConnections(xx: number, yy: number) {
        this.connectionMap = [];
        for (let y = 0; y < this.pixels.length - xx + 1; ++y) {
            const yList = [];
            for (let x = 0; x < this.pixels[0].length - yy + 1; ++x) {
                const gp = new GhostPixel(this, x * this.scale + 75, y * this.scale + 75);
                for (let j = 0; j < yy; ++j) {
                    for (let i = 0; i < xx; ++i) {
                        gp.pixels.push(this.pixels[y + j][x + i]);
                    }
                }
                // gp.pixels.push(this.pixels[y][x]);
                // for (let i = 1; i < xx; ++i) {
                //     gp.pixels.push(this.pixels[y][x + i - 1]);
                // }
                // for (let i = 1; i < yy; ++i) {
                //     gp.pixels.push(this.pixels[y + i - 1][x]);
                // }
                // gp.pixels.push(this.pixels[y + yy - 1][x + xx - 1]);
                gp.Compute();
                yList.push(gp);
            }
            this.connectionMap.push(yList);
        }
        for (let y = 0; y < this.connectionMap.length; ++y) {
            for (let x = 0; x < this.connectionMap.length; ++x) {
                const gp = this.connectionMap[y][x];
                if (y !== 0 && y < this.connectionMap.length - 1 && x !== 0 && x < this.connectionMap[0].length - 1) {
                    gp.pushConnections(this.connectionMap[y - 1][x]);
                    gp.pushConnections(this.connectionMap[y + 1][x]);
                    gp.pushConnections(this.connectionMap[y][x - 1]);
                    gp.pushConnections(this.connectionMap[y][x + 1]);
                } else if (y === 0) {
                    gp.pushConnections(this.connectionMap[y + 1][x]);
                    if (0 < x && x < this.connectionMap[0].length - 1) {
                        gp.pushConnections(this.connectionMap[y][x - 1]);
                        gp.pushConnections(this.connectionMap[y][x + 1]);
                    } else if (x === 0) {
                        gp.pushConnections(this.connectionMap[y][x + 1]);
                    } else if (x === this.connectionMap[0].length - 1) {
                        gp.pushConnections(this.connectionMap[y][x - 1]);
                    }
                } else if (y === this.connectionMap.length - 1) {
                    gp.pushConnections(this.connectionMap[y - 1][x]);
                    if (0 < x && x < this.connectionMap[0].length - 1) {
                        gp.pushConnections(this.connectionMap[y][x - 1]);
                        gp.pushConnections(this.connectionMap[y][x + 1]);
                    } else if (x === 0) {
                        gp.pushConnections(this.connectionMap[y][x + 1]);
                    } else if (x === this.connectionMap[0].length - 1) {
                        gp.pushConnections(this.connectionMap[y][x - 1]);
                    }
                } else if (x === 0) {
                    gp.pushConnections(this.connectionMap[y][x + 1]);
                    if (0 < y && y < this.connectionMap.length - 1) {
                        gp.pushConnections(this.connectionMap[y - 1][x]);
                        gp.pushConnections(this.connectionMap[y + 1][x]);
                    } else if (y === 0) {
                        gp.pushConnections(this.connectionMap[y + 1][x]);
                    } else if (y === this.connectionMap.length - 1) {
                        gp.pushConnections(this.connectionMap[y - 1][x]);
                    }
                } else if (x === this.connectionMap[0].length - 1) {
                    gp.pushConnections(this.connectionMap[y][x - 1]);
                    if (0 < y && y < this.connectionMap.length - 1) {
                        gp.pushConnections(this.connectionMap[y - 1][x]);
                        gp.pushConnections(this.connectionMap[y + 1][x]);
                    } else if (y === 0) {
                        gp.pushConnections(this.connectionMap[y + 1][x]);
                    } else if (y === this.connectionMap.length - 1) {
                        gp.pushConnections(this.connectionMap[y - 1][x]);
                    }
                }
            }
        }
    }
    Render(ctx: CanvasRenderingContext2D, p: number[]) {

        this.t += 0.1;
        if (this.t > 10) {
            // console.log(this.t);
            this.t = 0.0;
            if (!this.fail) { this.Update(); }
        }
        for (let y = 0; y < this.pixels.length; ++y) {
            for (let x = 0; x < this.pixels[0].length; ++x) {
                this.pixels[y][x].Render(ctx);
            }
        }

        if (this.pixelsHover) {
            for (let i = 0; i < this.pixelsHover.length; ++i) {
                this.pixelsHover[i].RenderHover(ctx);
            }
        }

        if (this.connectionMap) {
            for (let i = 0; i < this.connectionMap.length; ++i) {
                for (let j = 0; j < this.connectionMap.length; ++j) {
                    this.connectionMap[i][j].Render(ctx, p);
                }
            }
        }

        if (this.path && this.path.length > 0) {
            this.RenderPath(ctx, this.path);
            console.log('dddd');
        }

    }
    RenderPath(ctx: CanvasRenderingContext2D, path: GhostPixel[]) {
        ctx.strokeStyle = '#000000';
        ctx.lineWidth = 5;
        ctx.beginPath();
        ctx.moveTo((path[0].x), (path[0].y));
        for (let i = 1; i < path.length; ++i) {
            ctx.lineTo((path[i].x), (path[i].y));
        }
        ctx.stroke();
    }
}