import { NVector3, MathUtility } from '../../../../../lib';

export class BPath {

    static GetRandomPath(xMin: number, yMin: number, xMax: number, yMax: number, num: number = 10) {
        let vs = [];
        for (let i = 0; i < num; ++i) {
            let x = MathUtility.GetRandomIntInclusive(xMin, xMax);
            let y = MathUtility.GetRandomIntInclusive(yMin, yMax);
            vs.push(new NVector3(x, y, 0));
        }
        return vs;
    }

    constructor() {
        //
    }
}