import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class BallBounce extends DefinitionIndeCanvasCore {

    xpos: number = 100;
    ypos: number = 100;

    xspeed: number = 10;
    yspeed: number = 10;

    xdirection: number = 1;
    ydirection: number = 1;

    rad: number = 50;

    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
    }
    init() {
        //
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#eeeeee';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        // Update the position of the shape
        this.xpos = this.xpos + (this.xspeed * this.xdirection);
        this.ypos = this.ypos + (this.yspeed * this.ydirection);

        // Test to see if the shape exceeds the boundaries of the screen
        // If it does, reverse its direction by multiplying by -1
        if (this.xpos > this.width - this.rad || this.xpos < this.rad) {
            this.xdirection *= -1;
        }
        if (this.ypos > this.height - this.rad || this.ypos < this.rad) {
            this.ydirection *= -1;
        }

        // Draw the shape
        ctx.fillStyle = '#ff0000';
        ctx.beginPath();
        ctx.arc(this.xpos, this.ypos, this.rad, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();
    }
}