import { Bodies, World, Vector } from 'matter-js';
import { Scene } from '../Scene';
import { MGeometryBase } from './Base/MGeometryBase';
export class MCircle extends MGeometryBase {
    body: Matter.Body;
    scene: Scene;
    angle: number;
    position: Vector;
    // width: number;
    radius: number;
    constructor(scene: Scene, x: number, y: number, radius: number, isStatic: boolean = false) {
        super(scene);
        // this.width = width;
        this.radius = radius;
        const restitution = 0.5;
        const friction = 0.5;
        this.body = Bodies.circle(x, y, this.radius, { isStatic: isStatic, restitution: restitution, friction: friction });
        World.add(this.scene.world, this.body);
    }
    public Render(ctx: CanvasRenderingContext2D) {
        this.angle = this.body.angle;
        this.position = this.body.position;

        this.DrawCircle(ctx, this.position.x, this.position.y, this.radius, this.angle);
    }
    public DrawCircle(ctx: CanvasRenderingContext2D, x: number, y: number, radius: number, angle: number) {
        ctx.save();
        ctx.translate(x, y);
        ctx.rotate(angle);
        ctx.beginPath();
        ctx.arc(0, 0, this.radius, 0, Math.PI * 2);
        ctx.fill();
        ctx.restore();
    }
}