import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class AlgorithmComputerFinder extends DefinitionIndeCanvasCore {
    p: number[] = [0, 0];
    graph: Graph;
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.init();

    }
    init() {
        const data = [5, 4];
        const array = [
            [3, 1],
            [3, 2],
            [4, 3],
            [5, 3]
        ];
        let before = performance.now();
        this.graph = new Graph(data, array);
        let after = performance.now();
        console.log('time: ', after - before);
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.graph.Render(ctx, this.p);
        ctx.fillStyle = '#ff0000';
        ctx.beginPath();
        ctx.arc(this.p[0], this.p[1], 5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();

    }
    MouseMove(x: number, y: number) {
        this.p = [x, y];
    }
    MouseDown(x: number, y: number) {
        this.p = [x, y];
    }
}

// https://www.acmicpc.net/problem/1325

class Node {
    id: number = -1;
    visited: boolean = false;
    children: Node[] = [];
    constructor(id: number) {
        this.id = id;
    }
    Render(ctx: CanvasRenderingContext2D, p: number[]) {
        //
    }
}
class Graph {
    nodes: Node[] = [];
    constructor(data: number[], num: number[][]) {
        for (let i = 0; i < data[0]; ++i) { this.nodes.push(new Node(i)); }
        for (let i = 0; i < num.length; ++i) {
            const id0 = num[i][0] - 1;
            const id1 = num[i][1] - 1;
            this.nodes[id1].children.push(this.nodes[id0]);
        }
        this.ComputeConnections();
        console.log(this);
    }
    ComputeConnections() {
        for (let i = 0; i < this.nodes.length; ++i) {
            let count = this.BFS(this.nodes[i]);
            this.Reset();
            console.log(i + 1, count);
        }
    }
    BFS(n: Node) {
        let queue = [n];
        let count = 0;
        while (queue.length > 0) {
            let current = queue[0];
            queue.shift();
            if (current.visited) { continue; }
            current.visited = true;
            count++;
            for (let i = 0; i < current.children.length; ++i) {
                if (!current.children[i].visited) {
                    queue.push(current.children[i]);
                }
            }
        }
        return count;
    }
    Reset() {
        for (let i = 0; i < this.nodes.length; ++i) {
            this.nodes[i].visited = false;
        }
    }
    Render(ctx: CanvasRenderingContext2D, p: number[]) {
        //
    }
}