import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { SceneCreativeCoding } from '..';

export class AlgorithmOKVIRI extends DefinitionIndeCanvasCore {
    p: number[] = [0, 0];
    constructor(SceneCreativeCoding: SceneCreativeCoding) {
        super(SceneCreativeCoding);
        this.init();
    }
    init() {
        const data = 'DOG';
        let before = performance.now();
        const t = new OKVIRI(data);
        let after = performance.now();
        console.log('time: ', after - before);
        console.log(t);
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#ff0000';
        ctx.beginPath();
        ctx.arc(this.p[0], this.p[1], 5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();

    }
    MouseMove(x: number, y: number) {
        this.p = [x, y];
    }
    MouseDown(x: number, y: number) {
        this.p = [x, y];
    }
}

// https://www.acmicpc.net/problem/3054

class OKVIRI {
    constructor(word: string) {
        let length = word.length;
        let xDim = 5 + ((length - 1) * 4);
        console.log(xDim);
        console.log(length);
        for (let y = 0; y < 5; ++y) {
            let words = '';
            let index = 0;
            for (let x = 0; x < xDim; ++x) {
                if (y === 0 || y === 4) {
                    if ((x % 4) === 2) {
                        if (x < xDim - 5) {
                            words += '#';
                        } else {
                            words += '*';
                        }
                    } else {
                        words += '.';
                    }
                } else if (y === 1 || y === 3) {
                    if ((x % 2) === 1) {
                        if (x < xDim - 5) {
                            words += '#';
                        } else {
                            words += '*';
                        }
                    } else {
                        words += '.';
                    }
                } else {
                    if ((x % 4) === 0) {
                        if (x < xDim - 5) {
                            words += '#';
                        } else {
                            words += '*';
                        }
                    } else if (x % 2 === 0) {
                        words += word[index++];
                    } else {
                        words += '.';

                    }
                }
            }
            console.log(words);
        }
    }
}